<template>
	<div>
		<el-form-item
			label="请输入"
			label-width="50px"
			class="text-align-left">

			<el-input v-if="tagAdderData.tag=='profession'"
				v-model.trim="addTagData.profession.name">
			</el-input>

			<el-input v-if="tagAdderData.tag=='has_other_req'"
				v-model.trim="addTagData.has_other_req.name">
			</el-input>

			<el-input v-if="tagAdderData.tag=='personal_tag'"
				v-model.trim="addTagData.personal_tag.name">
			</el-input>

			<el-input v-if="tagAdderData.tag=='department'"
				v-model.trim="addTagData.department.name">
			</el-input>

      <el-input v-if="tagAdderData.tag=='job'"
        v-model.trim="addTagData.job.name">
      </el-input>
		</el-form-item>
		<el-button size="small" type="primary"
			@click="submitTag(addTagData[tagAdderData.tag].type,tagAdderData.tag)">
			添加
		</el-button>
	</div>
</template>

<script>
	export default {
		props: [
			'tagData'
		],
		data: function () {
			let self = this;
			return {
				tagAdderData: self.tagData,
				showAddAreaObj: {
					type_profession_sending: false,
					type_has_other_req_sending: false,
					type_personal_tag_sending: false,
					type_department_sending: false
				},
				addTagData: {
					'profession':
						{
							type: 1,
							name: ''
						},
					'has_other_req':
	          {
	            type: 3,
	            name: ''
            },
	        'personal_tag':
	          {
	            type: 2,
              name: ''
	           },
	         'department':
            {
	            type: 4,
	            name: ''
            },
          'job':
            {
              type: 5,
              name: ''
            }
				}
			}
		},
		created(){

		},
		methods: {
			submitTag(type,name){
			  console.log(`${type}----${name}`)
				let self = this;
				if(!self.showAddAreaObj['type_' + name +'_sending'] && self.addTagData[name].name != ''){
					self.showAddAreaObj['type_' + name +'_sending'] = true;
					self.$ajax.post(
						self.$api.getModuleHost('CRM', "customer/addTag"),
							{
								type: type,
								name: self.addTagData[name].name
							}
						)
					.then(function (data) {
						if (data.code == 1000) {
							let obj = {
								id: data.tag_id,
								name: data.name
							}
							self.addTagData[name].name = '';
							self.showAddAreaObj['type_' + name +'_sending'] = false;

							self.$emit('getUserDefined',{
								type_name: name,
								id: data.tag_id,
								name: data.name
							});
						} else {
							alert(data.msg);
						}
					}).catch(function (err) {
						console.error(err);
					});
				}
			}
		}
	}
</script>
<style lang="less">
	.hide{
		display: none;
	}
</style>



