<template>
    <div class="content" 
        :class="'position-' + city_data.single">
        <div class="pop_city_head">
            <span 
                v-for="(item,index) in letterArr"
                :class="type==index?'on':''"
                @click="changeType(index)">
                {{item}}
            </span>
            <i class="close-icon"
                @click.stop="hide()">
                X
            </i>
        </div>
        <div v-for="(item,index) in letterArr" 
            class="pop_city_body" :class="type==index?'':'hide'"
            >
            <div v-for="letterItem in item.split('')" class="pop_city_body_content">
                <p>
                    <strong>{{letterItem}}</strong>
                    <el-checkbox-group
                        v-show="single == 'multiple' && multiple_type == 'often_conf_city'"
                        v-model="often_conf_city_sel"
                        >
                        <el-checkbox 
                            class="item"
                            v-for="cityItem in cityList[letterItem]"
                            :label="cityItem.id"
                            :title="cityItem.name"
                            :disabled="cityItem.disabledOften"
                            @change="sendCityName(cityItem.id,cityItem.name)"
                            >
                            {{cityItem.name}}
                        </el-checkbox>
                    </el-checkbox-group>

                    <el-checkbox-group
                        v-show="single == 'multiple' && multiple_type == 'occasionally_conf_city'"
                        v-model="occasionally_conf_city_sel"
                        >
                        <el-checkbox 
                            class="item"
                            v-for="cityItem in cityList[letterItem]"
                            :label="cityItem.id"
                            :title="cityItem.name"
                            :disabled="cityItem.disabledOccasionally"
                            @change="sendCityName(cityItem.id,cityItem.name)"
                            >
                            {{cityItem.name}}
                        </el-checkbox>
                    </el-checkbox-group>

                    <el-radio-group
                        v-if="single == 'single'"
                        v-model="city_code"
                        @change="getCityName()"
                        >
                        <el-radio 
                            class="item"
                            v-for="cityItem in cityList[letterItem]" 
                            :label="cityItem.id+''"
                            >
                            {{cityItem.name}}
                        </el-radio>
                    </el-radio-group>
                </p>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    props: [
        'city_data'
    ],
    data: function(){
        let self = this;
        return{
            cityList: {},
            type: 0,
            letterArr: [                     
                'ABCD',
                'EFGH',
                'JKLM',
                'NPQR',
                'STWX',
                'YZ'
            ],
            city_code: self.city_data.city_code,
            city_name: self.city_name,
            hasCityList: 0,
            single: self.city_data.single,
            multiple_type: self.city_data.multiple_type,
            city_self: self.city_data.city_self,
            often_conf_city_sel: self.city_data.often_conf_city,
            occasionally_conf_city_sel: self.city_data.occasionally_conf_city
        }
    },
    created(){
        let self = this;
        console.log(self.hasCityList);
        if(self.hasCityList == 0){
            self.getCityList();    
        }
    },
    methods: {  
        getCityList(){
            let self = this;
            self.$ajax.get(self.$api.getModuleHost('CRM', "customer/getAllCity")).then(function (data) {
                if (data.code == 1000) {
                    self.cityList = data.data;
                    self.hasCityList = 1;
                    if(self.single=='multiple'){
                        self.setCityListDis();    
                    }
                }
            }).catch(function (err) {
                console.error(err);
            });
        },
        setCityList(city_data){
            let self = this;
            self.single = 'multiple';
            self.multiple_type = city_data.multiple_type;
            self.city_self = city_data.city_self;
            self.often_conf_city_sel = city_data.often_conf_city,
            self.occasionally_conf_city_sel = city_data.occasionally_conf_city;
            if(self.often_conf_city_sel.length || self.occasionally_conf_city_sel.length){
                for(let itemLetters of self.letterArr){
                    for(let itemLetter of itemLetters.split('')){
                        for(let item of self.cityList[itemLetter]){
                            for(let itemSel of self.city_self){
                                if(item.id == itemSel.id && itemSel.disabledOften){
                                    item.disabledOften = true;
                                    item.disabledOccasionally = false;
                                }
                                if(item.id == itemSel.id && itemSel.disabledOccasionally){
                                    item.disabledOften = false;
                                    item.disabledOccasionally = true;
                                }
                            }
                        }
                    }
                }
            }
        },
        setCityListDis(){
            let self = this;
            for(let itemLetters of self.letterArr){
                for(let itemLetter of itemLetters.split('')){
                    for(let item of self.cityList[itemLetter]){
                        item.disabledOften = false;
                        item.disabledOccasionally = false;
                    }
                }
            }
        },
        changeType(type){
            let self = this;
            self.type = type;
        },
        getCityName(){
            let self = this;
            let city_name = '';
            for(let itemLetters of self.letterArr){
                for(let itemLetter of itemLetters.split('')){
                    for(let item of self.cityList[itemLetter]){
                        if(item.id == self.city_code){
                            self.city_name = item.name;
                        }
                    }
                }
            }
            self.$emit('getCity',{
                city_code: self.city_code,
                city_name: self.city_name
            });
        },
        sendCityName(id,name){
            let self = this;
            let bool = false;
            for(let item of self[self.multiple_type+'_sel']){
                if(id == item){
                    bool = true;
                }
            }
            if(bool){
                self.setCityAll(id,name,self.multiple_type);
            } else {
                self.setCityAll(id,'',self.multiple_type);
            }
        },
        setCityAll(id,name,str){
            let self = this;
            let obj = {
                data: self[str+'_sel'],
                id: id,
                name: name,
                type: str
            };
            self.$emit('getCityAll',obj);
        },
        hide(){
            let self = this;
            self.$emit('hide');
        }
    }
}
</script>
<style lang="less" scoped>
    .hide{
        display: none;
    }
    .position-single{
        position: absolute;
        right: 0;
        top: 30px;
    }
    .position-multiple{
        position: absolute;
        left: 0;
        top: 30px;   
    }
    .content{
        font-size: 13px;
        line-height: 20px;
        z-index: 9998;
        width: 600px;
        text-align: left;
        color: #404040;
        border: 1px solid #ccc;
        border-radius: 2px;
        padding: 10px;
        background: #fff;
        box-shadow: 0 5px 15px -5px rgba(0,0,0,0.5);
        font-family: "Microsoft YaHei";
        -webkit-user-select: none;
        -moz-user-select: none;
    }
    .pop_city_head {
        padding-bottom: 5px;
    }
    .pop_city_head span {
        width: 60px;
        display: inline-block;
        cursor: pointer;
        text-align: center;
    }
    .pop_city_head span:first-child {
        text-align: left;
        margin-right: 10px;
    }
    .pop_city_body_content{
        
    }
    .pop_city_head span.on {
        color: #20a0ff;
        font-weight: bold;
    }
    .pop_city_body_content p strong {
        display: block;
        font-weight: bold;
    }
    .pop_city_body_content p .item {
        display: inline-block;
        width: 90px;
        text-overflow:ellipsis;
        white-space:nowrap;
        overflow:hidden;
        cursor: pointer;
    }
    .content .el-checkbox+.el-checkbox,.content .el-radio+.el-radio{
        margin-left: 0; 
    }
    .pop_city_body_content .el-checkbox,.pop_city_body_content .el-radio{
        margin-right: 10px;
    }
    .close-icon{
        cursor: pointer;
        position: absolute;
        right: 10px;
        top: 10px;
        font-size: 20px;
        color: #20a0ff;
        font-weight: bold;
    }
</style>


