<template>
	<el-form :model="orgreqinfo" :rules="editOneRulers" ref="form2" label-width="140px">
		<el-row justify="space-around">
			<el-col :span="8">
				<el-form-item :label="(baseData.year - 1 ) + '年会议总预算'">
					<el-input
						v-model="orgreqinfo.prev_year_budget"
						@blur="changeField('prev_year_budget',orgreqinfo.prev_year_budget)">
					</el-input>&nbsp;&nbsp;万
          <el-row>
            <div class="el-form-item__error reasonError" v-show="orgreqinfo.prev_year_budget>10000">不能大于10000万</div></el-row>
				</el-form-item>
			</el-col>
			<el-col :span="8">
				<el-form-item :label="(baseData.year) + '年会议总次数'">
					<el-input
						v-model="orgreqinfo.current_year_num"
						@blur="changeField('current_year_num',orgreqinfo.current_year_num)">
					</el-input>&nbsp;&nbsp;次
				</el-form-item>
			</el-col>
		</el-row>

		<el-row justify="space-around">
			<el-col :span="24">
				<el-form-item :label="(baseData.year+1) + '年预计会议'">
					<el-row>
						<el-radio-group
							v-model="orgreqinfo.next_conf_info"
							@change="resetReason();">
							<el-radio
								v-for="item in trend_arr" :label="item.id">
								{{item.name}}
							</el-radio>
						</el-radio-group>
					</el-row>
					<el-row>
						<el-col v-show="orgreqinfo.next_conf_info!=0" :span="24">
							<el-form-item prop="reason">
								<el-input  ref="budgetReason" type="textarea" placeholder="原因"
									v-model="orgreqinfo.reason"
									@blur="changeField('reason',orgreqinfo.reason)">

								</el-input>
							</el-form-item>
						</el-col>
					</el-row>
				</el-form-item>
			</el-col>
		</el-row>

		<el-row justify="space-around">
			<el-col :span="8">
				<el-form-item label="通过会小二的预定金额占比">
					<el-input
						v-model="orgreqinfo.hxe_percentage"
						@blur="changeField('hxe_percentage',orgreqinfo.hxe_percentage)">
					</el-input> %
				</el-form-item>
			</el-col>
		</el-row>
		<!--editing-->
		<el-row justify="space-around">
			<el-form-item label="举办会议消费区间" class="short-input">
				<el-col   :md="6" class="margin-bottom10"
					v-for="item in baseData.consumption">
					<span>{{item.name}}</span>
					<el-input
						v-model.number="consumption_interval['times_'+item.id]"
						@blur="setConsumptionInterval(item.id,consumption_interval['times_'+item.id])">
					</el-input> 次
				</el-col>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
		  	<el-form-item label="经常举办会议类型">
				<el-row justify="space-around">
				  	<el-checkbox-group
				  		v-model="orgreqinfo.often_conf_type"
				  		@change="setArrToString('often_conf_type')"
				  		>
					  	<el-checkbox
					  		v-for="item in baseData.meeting_type"
					  		:label="item.id+''"
					  		>
					  		{{item.name}}
					  	</el-checkbox>
					</el-checkbox-group>
				</el-row>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
		  	<el-form-item label="是否要异地办会">
				<el-row>
				  	<el-radio-group
				  		v-model="orgreqinfo.allopatry_conf"
				  		@change="resetAllopatryConfInfo();">
						<el-radio
							v-for="item in diffpalce_arr" :label="item.id">
							{{item.name}}
						</el-radio>
					</el-radio-group>
				</el-row>
				<div v-if="orgreqinfo.allopatry_conf==1">
				 	<el-row>
					  	<el-form-item
					  		label="常跨地区举办的两种会议"
					  		label-width="180px"
					  		class="text-align-left">
					  	</el-form-item>
				 	</el-row>
				 	<el-row class="margin-bottom10">
						<el-col :span="8">
						  	<el-select placeholder="请选择会议类型"
						  		v-model="orgreqinfo.allopatry_conf_info[0].id"
						  		@change="setAllopatryConfInfo()">
						  		<el-option
						  			label="请选择会议类型"
						  			:value="0">
						  		</el-option>
							  	<el-option
									v-for="item in baseData.meeting_type"
									:label="item.name"
									:value="item.id"
                  :disabled="orgreqinfo.allopatry_conf_info[1].id==item.id"
									>{{item.name}}
							    </el-option>
						  	</el-select>
						</el-col>
						<el-col :span="8">
						  	<el-form-item label="平均消费金额" label-width="100px">
								<el-input
									v-model.number="orgreqinfo.allopatry_conf_info[0].price"
									@blur="setAllopatryConfInfo()">

								</el-input> 万
						  	</el-form-item>
						</el-col>
						<el-col :span="8">
						  	<el-form-item label="大致次数" label-width="100px">
								<el-input
									v-model.number="orgreqinfo.allopatry_conf_info[0].num"
									@blur="setAllopatryConfInfo()">

								</el-input> 次
						  	</el-form-item>
						</el-col>
				 	</el-row>
				 	<el-row>

						<el-col :span="8">
						  	<el-select placeholder="请选择会议类型"
						  		v-model="orgreqinfo.allopatry_conf_info[1].id"
						  		@change="setAllopatryConfInfo()">
						  		<el-option
						  			label="请选择会议类型"
						  			:value="0">
						  		</el-option>
							  	<el-option
									v-for="item in baseData.meeting_type"
									:key="item.id"
									:label="item.name"
									:value="item.id"
                  :disabled="orgreqinfo.allopatry_conf_info[0].id==item.id"
                  >
							    </el-option>
						  	</el-select>
						</el-col>
						<el-col :span="8">
						  	<el-form-item label="平均消费金额" label-width="100px">
								<el-input
									v-model.number="orgreqinfo.allopatry_conf_info[1].price"
									@blur="setAllopatryConfInfo()">
								</el-input> 万
						  	</el-form-item>
						</el-col>
						<el-col :span="8">
						  	<el-form-item label="大致次数" label-width="100px">
								<el-input
									v-model.number="orgreqinfo.allopatry_conf_info[1].num"
									@blur="setAllopatryConfInfo()">

								</el-input> 次
						  	</el-form-item>
						</el-col>
					</el-row>
				 	<el-row>
					  	<el-form-item label="常举办城市" class="text-align-left">
					  	</el-form-item>
				 	</el-row>
				 	<el-row>
					 	<el-checkbox-group
					 		v-model="orgreqinfo.often_conf_city"
					 		@change="setArrToString('often_conf_city');disabledCity('OftenCityListSel');">
							<el-checkbox
								v-for="item in city_self"
								:label.number="item.id"
								:disabled="item.disabledOften"
								>
								{{item.name}}

							</el-checkbox>
					  	</el-checkbox-group>
					</el-row>
					<el-row>
						<span @click="showOftenCityList(true)" class="city-list add-data pointer">+添加城市</span>
						<cityList
							ref="OftenCityListSel"
							v-show="often_city_list_show"
							:city_data="city_data"
							@getCityAll="setCityAll"
							@hide="showOftenCityList(false)">
						</cityList>
					</el-row>
				  	<el-row>
				  		<el-form-item label="有时举办城市" class="text-align-left">
				  		</el-form-item>
				 	</el-row>
				 	<el-row>
					  	<el-checkbox-group
					  		v-model="orgreqinfo.occasionally_conf_city"
					  		@change="setArrToString('occasionally_conf_city');disabledCity('OccasionallyCityListSel');">
							<el-checkbox
								v-for="item in city_self"
								:label.number="item.id"
								:disabled="item.disabledOccasionally"
								>&nbsp;{{item.name}}

							</el-checkbox>
					  	</el-checkbox-group>
					</el-row>
				  	<el-row>
						<span @click="showOccasionallyCityList(true)" class="city-list add-data pointer">+添加城市</span>
						<cityList
							ref="OccasionallyCityListSel"
							v-show="occasionally_city_list_show"
							:city_data="city_data"
							@getCityAll="setCityAll"
							@hide="showOccasionallyCityList(false)">
						</cityList>
					</el-row>
				</div>
		  	</el-form-item>
		</el-row>

		<el-row justify="space-around">
		  	<el-form-item label="会议参会人类型">
				<el-checkbox-group
					v-model="orgreqinfo.participant_type"
					@change="setArrToString('participant_type')">
				  	<el-checkbox
				  		v-for="item in baseData.attendence_type"
				  		:label="item.id"
				  		>&nbsp;{{item.name}}
				  	</el-checkbox>
				</el-checkbox-group>
		  	</el-form-item>
		</el-row>

		<el-button v-if="status==2"
			type="primary"
			class="margin-bottom10"
			@click="save();" :disabled="isInSave">
			保存
		</el-button>
  	</el-form>
</template>

<script>

import cityList from '../common/cityList.vue'

export default {
  	props: [
  		'status',
			'mobile',
  		'customer_id',
  		'company_id',
  		'baseData',
  		'reqRule',
  		'consumptionRule',
  		'city'
  	],
  	data: function () {
  		let self = this;
			return {
        isInSave:false,
        isFouce:false,
				orgreqinfo:{
					prev_year_budget: '',
					next_conf_info: 0,
					allopatry_conf: '',
					current_year_num: '',
					hxe_percentage: '',
					allopatry_conf_info: [
						{
							id: 0,
							num: '',
							price:''
						},
						{
							id: 0,
							num: '',
							price:''
						}
					],
					often_conf_type: [],
					often_conf_city: [],
					occasionally_conf_city: [],
					participant_type: [],
					consumption_interval: [],
					reason: ''
				},
				selected_often_conf_city: [],
				often_conf_city_sel: [],
				selected_occasionally_conf_city: [],
				occasionally_conf_city_sel: [],
				city_data: {
					single: 'multiple',
					multiple_type: '',
					often_conf_city: [],
					occasionally_conf_city: [],
					city_self: self.city
				},
			  diffpalce_arr: [
            {
                id: 1,
                name: "有"
            },
            {
                id: 2,
                name: "无"
            }
        ],
        trend_arr: [
        	{
                id: 1,
                name: "增长"
            },
            {
                id: 2,
                name: "持平"
            },
            {
                id: 3,
                name: "减少"
            },
        ],
        consumption_interval: {},		//消费区间次数
        self_consumption_interval: [],
        often_city_list_show: false,
        occasionally_city_list_show: false,
        city_self: self.city,
        next_conf_info: 0,
        next_conf_info_times: 0, //过滤第一次自动设置值
        allopatry_conf_times: 0, //过滤第一次自动设置值
        showAllopatryConfInfo: false,
        //以下是提交的数据
        reqinfo: {},
        editOneRulers:{
//          elementUI原生数据验证
          reason:[
            { required: true, message: '请输入原因', trigger: 'blur' }
          ]
        }
			}
  	},
		mounted(){
  		let self = this;
  		if(self.status == 2){
  			self.getOrgReqInfo();
  		} else { // 在创建页面的时候把本地数据赋值给双向绑定的数据
				self.setLocalData();
			}
      self.$root.Bus.$off('formOneValidate')
      self.$root.Bus.$on('formOneValidate',self.formOneValidate)
  	},
  	watch: {
  		'$route' (to, from) {
	        let self = this;
					self.resetData();
		  		if(self.status == 2){
		  			self.getOrgReqInfo();
		  		} else { // 在创建页面的时候把本地数据赋值给双向绑定的数据
						self.setLocalData();
					}
	    }
  	},
  	methods: {
			resetData() {
				this.next_conf_info_times = 0;
				this.allopatry_conf_times = 0;
				this.selected_often_conf_city = [];
				this.often_conf_city_sel = [];
				this.selected_occasionally_conf_city = [];
				this.occasionally_conf_city_sel = [];
				this.consumption_interval = {};
				this.reqinfo = {};
				this.self_consumption_interval = [];
				this.often_city_list_show = false;
				this.occasionally_city_list_show = false;
				this.showAllopatryConfInfo = false;
				this.next_conf_info = 0;
				this.next_conf_info_times = 0;
				this.allopatry_conf_times = 0;
				this.orgreqinfo = {
					prev_year_budget: '',
					next_conf_info: 0,
					allopatry_conf: '',
					current_year_num: '',
					hxe_percentage: '',
					allopatry_conf_info: [
						{
							id: 0,
							num: '',
							price:''
						},
						{
							id: 0,
							num: '',
							price:''
						}
					],
					often_conf_type: [],
					often_conf_city: [],
					occasionally_conf_city: [],
					participant_type: [],
					consumption_interval: [],
					reason: ''
				}

			},
			setLocalData() {
				let self = this;
				let storageId = 'draft_' + self.mobile + self.company_id // 缓存id
				if (localStorage.getItem(storageId)) {
					let localData = JSON.parse(localStorage.getItem(storageId))
					localData = localData['reqinfo'];
					if(localData) {
						for (let k in localData) {
							if (
									k == 'often_conf_type' ||
									k == 'often_conf_city' ||
									k == 'occasionally_conf_city' ||
									k == 'participant_type'
								//	k == 'consumption_interval'
								) { // 复选框的值是用逗号(‘,’)分割存储的，需要转成数组格式才能默认选中

								self.orgreqinfo[k] = localData[k].split(',');
								self.orgreqinfo[k].forEach(function(item, index, array){
									return array[index] = Number(item);
								});
							} else {
									if (k == 'consumption_interval') { //  给消费区双向绑定的数据赋值
										self.self_consumption_interval  = localData[k]
										for (let i in self.self_consumption_interval){
												self.consumption_interval['times_' +self.self_consumption_interval[i]['id']] = Number(self.self_consumption_interval[i]['num']);
										}
									} else {
										self.orgreqinfo[k] = localData[k];
									}
							}
						}
					}
					self.$refs['form2'].resetFields()
				}
			},
  		getOrgReqInfo(){
			let self = this;
			self.$ajax.get(
				self.$api.getModuleHost('CRM', "customer/reqInfo"),
				{
					company_id: self.company_id,
					customer_id: self.customer_id
				}
			)
			.then(function (data) {
				if (data.code == 1000) {

					self.orgreqinfo.prev_year_budget = data.data.prev_year_budget;
					self.orgreqinfo.next_conf_info = data.data.next_conf_info == ''?self.orgreqinfo.next_conf_info:data.data.next_conf_info;

					self.next_conf_info = data.data.next_conf_info == ''?self.next_conf_info:data.data.next_conf_info;
					self.reason = data.data.reason;

					self.orgreqinfo.allopatry_conf = data.data.allopatry_conf;
					if (data.data.allopatry_conf==1) self.showAllopatryConfInfo=true
					self.orgreqinfo.current_year_num = data.data.current_year_num;
					self.orgreqinfo.hxe_percentage = data.data.hxe_percentage;
					self.orgreqinfo.reason = data.data.reason;

					if(data.data.participant_type != ''){
						self.orgreqinfo.participant_type = data.data.participant_type.split(',');
						self.orgreqinfo.participant_type.forEach(function(item, index, array){
							return array[index] = Number(item);
						});
					} else {
						self.orgreqinfo.participant_type = [];
					}
					if(data.data.often_conf_type != ''){
						self.orgreqinfo.often_conf_type = data.data.often_conf_type.split(',');
					} else {
						self.orgreqinfo.often_conf_type = [];
					}
					if(data.data.often_conf_city != ''){
						self.orgreqinfo.often_conf_city = data.data.often_conf_city.split(',');
						self.orgreqinfo.often_conf_city.forEach(function(item, index, array){
							return array[index] = Number(item);
						});
					} else {
						self.orgreqinfo.often_conf_city = [];
					}
					if(data.data.occasionally_conf_city != '' ){
						self.orgreqinfo.occasionally_conf_city = data.data.occasionally_conf_city.split(',');

						self.orgreqinfo.occasionally_conf_city.forEach(function(item, index, array){
							return array[index] = Number(item);
						});
					} else {
						self.orgreqinfo.occasionally_conf_city = [];
					}

					if(data.data.allopatry_conf_info[0]){
					  for (var p in data.data.allopatry_conf_info[0]){
              self.orgreqinfo.allopatry_conf_info[0][p]=data.data.allopatry_conf_info[0][p]
            }
					} else {
//						self.orgreqinfo.allopatry_conf_info[0] = {
//							id: 0,
//							num: '',
//							price:''
//						}
            self.orgreqinfo.allopatry_conf_info[0].id=0;
            self.orgreqinfo.allopatry_conf_info[0].num="";
            self.orgreqinfo.allopatry_conf_info[0].price="";

					}

					if(data.data.allopatry_conf_info[1]){
						for (var p in data.data.allopatry_conf_info[1]){
              self.orgreqinfo.allopatry_conf_info[1][p]=data.data.allopatry_conf_info[1][p]
            }
					} else {
//						self.orgreqinfo.allopatry_conf_info[1] = {
//							id: 0,
//							num: '',
//							price:''
//						}
            self.orgreqinfo.allopatry_conf_info[1].id=0;
            self.orgreqinfo.allopatry_conf_info[1].num="";
            self.orgreqinfo.allopatry_conf_info[1].price="";
					}



					//if(data.data.consumption_interval){
						self.orgreqinfo.consumption_interval = data.data.consumption_interval;
					//}
					self.self_consumption_interval = data.data.consumption_interval;

					for(let item of self.orgreqinfo.consumption_interval){
						self.consumption_interval['times_' + item.id] = Number(item.num);
					}

					self.disabledCity();
				}
			}).catch(function (err) {
				console.error(err);
			});
		},
		resetReason(){

			let self = this;
			self.next_conf_info_times++;
			self.reqinfo.reason = self.orgreqinfo.reason;
			self.changeField('next_conf_info',self.orgreqinfo.next_conf_info);
			if (self.next_conf_info_times > 1) {
				self.orgreqinfo.reason = '';
			}
		},
		resetAllopatryConfInfo(){
			let self = this;
			self.allopatry_conf_times++
			if (self.allopatry_conf_times >= 1){
				if(self.orgreqinfo.allopatry_conf == 2){
					self.showAllopatryConfInfo = false;
					self.orgreqinfo.allopatry_conf_info = [
						{
							id: 0,
							num: '',
							price:''
						},
						{
							id: 0,
							num: '',
							price:''
						}
					];

					self.setAllopatryConfInfo();

					self.orgreqinfo.often_conf_city = [];
					self.orgreqinfo.occasionally_conf_city = [];

					self.setArrToString('often_conf_city');
					self.setArrToString('occasionally_conf_city');

					self.disabledCity();
				} else {
					self.showAllopatryConfInfo = true;
				}
        self.changeField('allopatry_conf',self.orgreqinfo.allopatry_conf);
			}

		},
		changeField(feildName,feildVal){
  		let self = this;
  		if(self.status == 1){
  			self.$emit('getReqInfo',arguments);
  		} else {
  			self.reqinfo[feildName] = feildVal;
  		}
  	},
  	setConsumptionInterval(id, num){
  		let self = this;
  		let flag = false;
  		for(let item of self.self_consumption_interval){
  			if(!flag && item.id === id){
  				item.num = num;
  				flag = true;
  			}
  		}
  		if(!flag){
  			let obj = {
    			id: id,
    			num: num
    		};

    		self.self_consumption_interval.push(obj);
  		}
  		arguments[0] = 'consumption_interval';
  		arguments[1] = self.self_consumption_interval;
  		if(self.status == 1){
  			self.$emit('getReqInfo',arguments);
  		} else {
  			self.reqinfo[arguments[0]] = arguments[1];
  		}
  	},
  	setAllopatryConfInfo(){
  		let self = this;
			console.log(1);
  		arguments[0] = 'allopatry_conf_info';
  		arguments[1] = self.orgreqinfo.allopatry_conf_info;
  		if(self.status == 1){
  			self.$emit('getReqInfo',arguments);
  		} else {
  			self.reqinfo[arguments[0]] = arguments[1];
  		}
  	},
  	setArrToString(str){
  		let self = this;
  		arguments[0] = str;
  		arguments[1] = self.orgreqinfo[str].join(",");
  		if(self.status == 1){
  			self.$emit('getReqInfo',arguments);
  		} else {
  			self.reqinfo[arguments[0]] = arguments[1];
  		}
  	},
  	save(){
			let self = this;
			var validateMsg = self.validateForm()
			if (validateMsg !=""){
				self.$message({
          message: validateMsg,
          type: 'warning',
					duration: 1000
        });
				return;
			}
//			是否要异地办会为无时所传参数为空数组
			if (self.orgreqinfo.allopatry_conf==2){
        self.reqinfo.allopatry_conf_info=[]
      }
      if (self.isInSave)
        return
      self.isInSave=true;
			self.$ajax.post(
				self.$api.getModuleHost('CRM', "customer/saveCustomer"),
				{
					customer_id: self.customer_id,
					company_id: self.company_id,
					reqinfo: JSON.stringify(self.reqinfo)
				}
			)
			.then(function (data) {
				if (data.code == 1000) {
					self.$emit('getBool');
				} else {
					alert(data.msg);
				}
        self.isInSave=false;
			}).catch(function (err) {
				console.error(err);
        self.isInSave=false;
			});
		},
		validateForm() {
			let ruleDesc = "";
			let self = this;
			for (let k in self.reqRule){ // 验证需求信息中的格式
				let ruleName = self.reqRule[k]['name'] // 验证的字段名称
				if(self.reqinfo[ruleName]){ // 如果验证字段名称存在
					if(ruleName == "next_conf_info") {
						if (!self.$refs.budgetReason.value) {
							return ruleDesc = (self.baseData.year + 1)+"年会议总预算的原因不能为空";
						}
					} else {
						let rule = self.reqRule[k]['rule'];
						if (!rule.test(self.reqinfo[ruleName])){
							ruleDesc = self.reqRule[k]['desc'];
							return ruleDesc;
						}
					}
				}
			}

			// 消费区间单独验证
			if (self.reqinfo['consumption_interval']) {
				for (let k in self.reqinfo['consumption_interval']) {
					var num = self.reqinfo['consumption_interval'][k]['num']
					var id = self.reqinfo['consumption_interval'][k]['id']
					if(num){
						if (!/^[0-9]+$/.test(num)) {
							ruleDesc = self.consumptionRule['cons_'+id];
							return ruleDesc;
						}
					}
				}
			}
			// 常跨地区举办的两种会议单独验证
			if (self.reqinfo['allopatry_conf_info']) {
				for (let k in self.reqinfo['allopatry_conf_info']) {
					var num = self.reqinfo['allopatry_conf_info'][k]['num'];
					var price = self.reqinfo['allopatry_conf_info'][k]['price']
					if (price) {
						if (!/^[0-9]+$/.test(price)) {
							ruleDesc = "常跨地区举办的两种会议金额必须正整数"
							return ruleDesc;
						}
					}
					if (num) {
						if (!/^[0-9]+$/.test(num)) {
							ruleDesc = "常跨地区举办的两种会议次数必须正整数"
							return ruleDesc;
						}
					}
				}
			}
			return ruleDesc;
		},
		showOftenCityList(bool){
			let self = this;
			self.often_city_list_show = bool;
			if(self.occasionally_city_list_show){
				self.occasionally_city_list_show = false;
			}
			if(self.often_city_list_show){
				self.disabledCity('OftenCityListSel');
			}
		},
		showOccasionallyCityList(bool){
			let self = this;
			self.occasionally_city_list_show = bool;
			if(self.often_city_list_show){
				self.often_city_list_show = false;
			}
			if(self.occasionally_city_list_show){
				self.disabledCity('OccasionallyCityListSel');
			}
		},
		setCityAll(opt){
			let self = this;
			let obj = {
				city_code: opt.id,
				city_name: opt.name
			};
			let bool = false;
			self.orgreqinfo[opt.type] = opt.data;
			for(let item of self.city_self){
				if(item.id == obj.city_code){
					bool = true;
				}
			}
			if(!bool){
				self.city_self.push({
					id: obj.city_code,
					name: obj.city_name
				});
			}
			if(opt.type=='often_conf_city'){
				self.setArrToString('often_conf_city');
				self.disabledCity('OftenCityListSel');
			} else {
				self.setArrToString('occasionally_conf_city');
				self.disabledCity('OccasionallyCityListSel');
			}
		},
		/*addCityName(str,obj){
			let self = this;
			let bool = false;
			for(let item of self['selected_'+str]){
				if(item.city_code == obj.city_code){
					bool = true;
				}
			}
			if(!bool){
				self[str+'_sel'].push(obj);
			}
		},
		deleteCityName(str,obj){
			let self = this;
			let index = 0;
			for (let i = 0; i < self['selected_'+str].length; i++) {
				if (self['selected_'+str][i].city_code == obj.city_code){
					index = i;
				}
			}
			if(index>0){
				self['selected_'+str].splice(index,1);
			} else {
				for (let i = 0; i < self[str+'_sel'].length; i++) {
					if (self[str+'_sel'][i].city_code == obj.city_code){
						index = i;
					}
				}
				self[str+'_sel'].splice(index,1);
			}
		},*/
		disabledCity(str,city){
			let self = this;
			if (city){
				self.city_self = city;
			}
			//console.log(self.orgreqinfo.often_conf_city);
			for(let itemOrg of self.city_self){
				itemOrg.disabledOften = false;
				itemOrg.disabledOccasionally = false;


				if(self.orgreqinfo.often_conf_city.length>0){
					for(let item of self.orgreqinfo.often_conf_city){
						if( item == itemOrg.id ){
							itemOrg.disabledOccasionally = true;
						}
					}
				}
				if(self.orgreqinfo.occasionally_conf_city.length>0){
					for(let item of self.orgreqinfo.occasionally_conf_city){
						if( item == itemOrg.id ){
							itemOrg.disabledOften = true;
						}
					}
				}
			}
			if(str){
				self.city_data.often_conf_city = self.orgreqinfo.often_conf_city;
				self.city_data.occasionally_conf_city = self.orgreqinfo.occasionally_conf_city;
				self.city_data.city_self = self.city_self;
				if(str == 'OftenCityListSel'){
					self.city_data.multiple_type = 'often_conf_city';
					if(self.often_city_list_show){
						self.$refs[str].setCityList(self.city_data);
					}
					if(self.occasionally_city_list_show){
						self.occasionally_city_list_show = false;
					}
				} else {
					self.city_data.multiple_type = 'occasionally_conf_city';
					if(self.occasionally_city_list_show){
						self.$refs[str].setCityList(self.city_data);
					}
					if(self.often_city_list_show){
						self.often_city_list_show = false;
					}
				}
			}
		},
    formOneValidate(){
      this.$refs['form2'].validate()
    }
  	},
  	components: {
  		cityList
  	}
}
</script>
<style lang="less">
	.city-list{
		position: relative;
	}
</style>



