<template>
	<el-form ref="form4" :model="orgcompanyfeature" :rules="editThreeRulers" label-width="140px">
		<el-row justify="space-around">
			<el-form-item label="企业常办会部门">
				<el-row>
					<el-checkbox-group
						v-model="orgcompanyfeature.conf_department"
						@change="setArrToString('conf_department')">
						<el-checkbox
							v-for="item in baseData.department"
							:label="item.id+''">
							{{item.name}}

						</el-checkbox>
				  	</el-checkbox-group>
				  	<p class="add-data">
						<span @click="showAddArea('ConfDepartment');">
              <i  :class="{'el-icon-minus':showConfDepartmentUserDef,'el-icon-plus':!showConfDepartmentUserDef}"></i>
              {{showConfDepartmentUserDef==true?' 收起':' '}}添加自定义企业常办会部门
              </span>
					</p>
				</el-row>

				<el-row>
					<el-col :span="12">
						<tagAdder
							v-if="showConfDepartmentUserDef"
							:tagData="tagData.department"
							@getUserDefined="setUserDefined">
						</tagAdder>
					</el-col>
				</el-row>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="企业决策方式">
				<el-select
					v-model="orgcompanyfeature.decision_type"
					@change="changeField('decision_type',orgcompanyfeature.decision_type);">
					<el-option
						label="请选择"
						:value.number="0"
						>

					</el-option>
					<el-option
						v-for="item in baseData.decision_method"
						:label="item.name"
						:value.number="item.id"
						>

					</el-option>
			  	</el-select>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="是否是采购决策人">
				<el-row>
					<el-radio-group
						v-model="orgcompanyfeature.is_decisionmaker"
						@change="resetDecisionUser();">
						<el-radio
					  		v-for="item in is_decisionmaker_arr"
					  		:label="item.id">
					  		{{item.name}}
					  	</el-radio>
					</el-radio-group>
				</el-row>
				<div v-if="showDecisionUser">
					<el-row class="margin-bottom10">
						<el-col :span="10">
							<el-form-item label="决策人姓名"
								class="text-align-left"
								label-width="90px">
								<el-input
									v-model="orgcompanyfeature.decision_user.name"
									@blur="setDecisionUser();">
								</el-input>
							</el-form-item>
						</el-col>
						<el-col :span="10">
							<el-form-item label="决策人所在部门"
								class="text-align-left"
								label-width="110px">
								<el-select
									v-model="orgcompanyfeature.decision_user.department_id"
									@change="setDecisionUser();">
									<el-option
										label="请选择部门"
										:value="0"
										>
									</el-option>
									<el-option v-for="item in baseData.department"
										:label="item.name"
										:value="item.id">

									</el-option>
								</el-select>
							</el-form-item>
						</el-col>
					</el-row>
					<el-row>
						<el-col :span="10">
							<el-form-item label="决策人职位"
								class="text-align-left"
								label-width="90px">
								<el-select
									v-model="orgcompanyfeature.decision_user.job_id"
									@change="setDecisionUser();">
									<el-option
										label="请选择职位"
										:value="0"
										>
									</el-option>
									<el-option
										v-for="item in baseData.job"
										:label="item.name"
										:value="item.id">

									</el-option>
								</el-select>
							</el-form-item>
						</el-col>
						<el-col :span="10">
							<el-form-item label="决策人联系方式"
								class="text-align-left"
                prop="decision_user.phone"
								label-width="110px">
								<el-input
									v-model="orgcompanyfeature.decision_user.phone"
									@blur="setDecisionUser();">
								</el-input>
							</el-form-item>
						</el-col>
					</el-row>
				</div>
			</el-form-item>
		</el-row>

		<el-row>
			<el-form-item label="了解到客户内部的采购决策情况" label-width="250px">
			</el-form-item>
			<el-form-item>
				<el-input type="textarea" placeholder="请输入了解到客户内部的采购决策情况"
					v-model="orgcompanyfeature.decision_comment"
					@blur="changeField('decision_comment',orgcompanyfeature.decision_comment)">

				</el-input>
			</el-form-item>
		</el-row>

		<el-button v-if="status==2"
			type="primary"
			class="margin-bottom10"
			@click="save();" :disabled="isInSave">
			保存
		</el-button>
	</el-form>
</template>

<script>

import tagAdder from '../common/tagAdder.vue'

export default {
	props: [
		'status',
		'customer_id',
		'company_id',
		'mobile',
		'baseData'
	],
	data: function () {
    let contactWay = (ruler,value,callback)=>{
      let phoneReg = /^0\d{2,3}-?\d{7,8}$/;
      let mobileReg = /^1[34578]\d{9}$/;
      if (!mobileReg.test(value)&&!phoneReg.test(value)&&value!=''){
        callback(new Error('联系方式必须为手机或固定电话'))
      }
    }
		return {
      isInSave:false,
      editThreeRulers:{
        'decision_user.phone':[
          {
            validator:contactWay,
            trigger:'blur'
          }
        ]
      },
			orgcompanyfeature:{
				conf_department: [],
				decision_comment: '',
				decision_type: 0,
				decision_user: {
					name: '',
					department_id: 0,
					job_id: 0,
					phone: ''
				},
				is_decisionmaker: ''
			},
			showDecisionUser: false,
			is_decisionmaker_arr: [
            	{
                    id: 1,
                    name: "是"
                },
                {
                    id: 2,
                    name: "否"
                }
            ],
			showConfDepartmentUserDef: false,
            tagData: {
            	'department':
	        		{
	        			tag: 'department',
	        			tagName: '自定义公司常办会部门'
	        		}
            },
            //以下是提交的数据
            companyfeature:{}
		}
	},
	mounted(){
		let self = this;
		if(self.status == 2){
			self.getOrgCompanyFeature();
		}  else {
				self.setLocalData(); // 在创建页面的时候把本地数据赋值给双向绑定的数据
		}
    self.$root.Bus.$off('formThreeValidate')
    self.$root.Bus.$on('formThreeValidate',self.formThreeValidate)
	},
	watch: {
	    '$route' (to, from) {
	  		let self = this;
				self.resetData();
				if(self.status == 2){
					self.getOrgCompanyFeature();
				} else {
						self.setLocalData(); // 在创建页面的时候把本地数据赋值给双向绑定的数据
				}
	    }
	},
	methods: {
		resetData(){
			let self = this;
			self.orgcompanyfeature = {
				conf_department: [],
				decision_comment: '',
				decision_type: 0,
				decision_user: {
					name: '',
					department_id: 0,
					job_id: 0,
					phone: ''
				},
				is_decisionmaker: ''
			};
			self.showDecisionUser = false;

			self.showConfDepartmentUserDef = false;
  		//以下是提交的数据
      self.companyfeature =  {}
		},
		setLocalData() {
			let self = this;
			let storageId = 'draft_' + self.mobile + self.company_id // 缓存id
			if (localStorage.getItem(storageId)) {
				let localData = JSON.parse(localStorage.getItem(storageId))
				localData = localData['companyfeature'];
				for (let k in localData) {
					if (
							k == 'conf_department'
						) { // 复选框的值是用逗号(‘,’)分割存储的，需要转成数组格式才能默认选中
						self.orgcompanyfeature[k] = localData[k].split(',');
					} else {
						self.orgcompanyfeature[k] = localData[k];
					}
				}
        self.$refs['form4'].resetFields()
			}
		},
		showAddArea(tag){
			let self = this;
			self['show' + tag + 'UserDef'] = !self['show' + tag + 'UserDef'];
		},
		setUserDefined(opt){
			let self = this;
			self.$emit('getUserDefined',opt);
		},
		getOrgCompanyFeature(){
			let self = this;
			self.$ajax.get(
				self.$api.getModuleHost('CRM', "customer/userCompanyFeature"),
				{
					customer_id: self.customer_id,
					company_id: self.company_id
				}
			)
			.then(function (data) {
				if (data.code == 1000) {
					self.orgcompanyfeature.decision_comment = data.data.decision_comment;
					self.orgcompanyfeature.decision_type = data.data.decision_type;
					self.orgcompanyfeature.is_decisionmaker = data.data.is_decisionmaker;
					if(data.data.conf_department!=''){
						self.orgcompanyfeature.conf_department = data.data.conf_department.split(',');
					} else {
						self.orgcompanyfeature.conf_department = []
					}
					if(data.data.decision_user != ''){
						self.orgcompanyfeature.decision_user = data.data.decision_user;
					} else {
						self.orgcompanyfeature.decision_user = {
								name: '',
								department_id: 0,
								job_id: 0,
								phone: ''
							}
					}
				}
			}).catch(function (err) {
				console.error(err);
			});
		},
		changeField(feildName,feildVal){
  		let self = this;
  		if(self.status == 1){
    		self.$emit('getCompanyFeature',arguments);
    	} else {
			self.companyfeature[feildName] = feildVal;
    	}
  	},
  	resetDecisionUser(){
  		let self = this;
  		if(self.orgcompanyfeature.is_decisionmaker == 1){
  			self.showDecisionUser = false;
  			// self.orgcompanyfeature.decision_user = {
				// 	name: '',
				// 	department_id: 0,
				// 	job_id: 0,
				// 	phone: ''
				// };
				//self.setDecisionUser();
  		} else {
  			self.showDecisionUser = true;
  		}
			self.changeField('is_decisionmaker',self.orgcompanyfeature.is_decisionmaker);
  	},
		setArrToString(str){
			let self = this;
			arguments[0] = str;
    		arguments[1] = self.orgcompanyfeature[str].join(",");
    		if(self.status == 1){
    			self.$emit('getCompanyFeature',arguments);
    		} else {
    			self.companyfeature[arguments[0]] = arguments[1];
    		}
		},
		setDecisionUser(){
			let self = this;
    		arguments[0] = 'decision_user';
    		arguments[1] = self.orgcompanyfeature.decision_user;
    		if(self.status == 1){
    			self.$emit('getCompanyFeature',arguments);
    		} else {
    			self.companyfeature[arguments[0]] = arguments[1];
    		}
		},
		save(){
			let self = this;
      let phoneReg = /^0\d{2,3}-?\d{7,8}$/;
      let mobileReg = /^1[34578]\d{9}$/;
      if (!mobileReg.test(self.orgcompanyfeature.decision_user.phone)
        &&!phoneReg.test(self.orgcompanyfeature.decision_user.phone)
        &&self.orgcompanyfeature.decision_user.phone!=''){
        self.$message({
          message: "决策人联系方式必须为手机或固定电话",
          type: 'warning',
          duration: 1000
        });
        return false;
      }
      if (self.isInSave)
        return
      self.isInSave=true;
			self.$ajax.post(
				self.$api.getModuleHost('CRM', "customer/saveCustomer"),
				{
					customer_id: self.customer_id,
					company_id: self.company_id,
					companyfeature: JSON.stringify(self.companyfeature)
				}
			)
			.then(function (data) {
				if (data.code == 1000) {
					self.$emit('getBool');
				} else {
					alert(data.msg);
				}
        self.isInSave=false;
			}).catch(function (err) {
				console.error(err);
        self.isInSave=false;
			});
		},
    formThreeValidate(){
      this.$refs['form4'].validate()
    }
	},
	components: {
		tagAdder
	}
}
</script>
<style lang="less">

</style>



