<template>
	<div class="padding-bottom50">
		<div v-if="!creating">
			<div class="count-down-wrap">{{countDownDesc}}</div>
      <h2>办会人基本信息
        <el-button class="header-btn" v-if="status == 1 && couldBeCreated"
          type="primary"
          @click="createUser();" :disabled="isCreating">
          创建
        </el-button>
        <div class="header-btn" v-if="status == 1 && !couldBeCreated">
          <span class="createTip">您至少需要完善6项客户信息，才能完成创建</span>
          <el-button class=""
            type="default"
            @click="sixLimited();" disabled>
            创建
          </el-button>
        </div>
        </h2>
			<baseInfoEdit
				:status="status"
				:customer_id="customer_id"
				:company_id="company_id"
				:mobile="mobile"
				:baseData="baseData"
				@getUserBasicInfo="setUserBasicInfo"
				@getUserBasicInfoAll="setUserBasicInfoAll"
				@getUserDefined="setUserDefined">
			</baseInfoEdit>

			<div class="hr"></div>

			<h2>会议需求分析</h2>
			<requirementEditPart1
				:status="status"
				:customer_id="customer_id"
				:company_id="company_id"
				:mobile="mobile"
				:baseData="baseData"
				:reqRule="reqRule"
				:city="city"
				@getReqInfo="setReqInfo"
				ref="part1"
				>
			</requirementEditPart1>

			<div class="hr"></div>
      <h2>客户采购分析</h2>
			<requirementEditPart2
				:status="status"
				:customer_id="customer_id"
				:company_id="company_id"
				:mobile="mobile"
				:baseData="baseData"
				:area="area"
				@getUserFeature="setUserFeature"
				@getUserDefined="setUserDefined">
			</requirementEditPart2>

			<div class="hr"></div>
      <h2>企业采购分析</h2>
			<requirementEditPart3
				:status="status"
				:customer_id="customer_id"
				:company_id="company_id"
				:mobile="mobile"
				:baseData="baseData"
				@getCompanyFeature="setCompanyFeature"
				@getUserDefined="setUserDefined">

			</requirementEditPart3>

			<!-- <el-button v-if="status == 1"
				type="primary"
				@click="createUser();" >
				保存
			</el-button> -->

			<el-button v-if="status == 1 && couldBeCreated"
				type="primary"
				@click="createUser();" :disabled="isCreating">
				创建
			</el-button>

			<el-button v-if="status == 1 && !couldBeCreated"
				type="default"
				@click="sixLimited();" disabled>
				创建
			</el-button>
		</div>

		<div v-else>
			<p>
				{{countDownDesc}}
			</p>
		</div>
	</div>
</template>

<script>
import baseInfoEdit from './baseInfoEdit.vue'
import requirementEditPart1 from './requirementEditPart1.vue'
import requirementEditPart2 from './requirementEditPart2.vue'
import requirementEditPart3 from './requirementEditPart3.vue'

export default {
	data: function () {
		let self = this;
		return {
			creating: false,
      isCreating:false, //是否处于提交状态
			time: '',
			timer: null, // 定时器对象
			status: 1,				//1 创建  2 编辑
			customer_id: '',
			company_id: self.$route.params['company_id'],
			mobile: self.$route.params['mobile'],
			baseData: {},		//基础数据
			city: [],
			area: [],
			storageId: 'draft_' + self.$route.params['mobile'] + self.$route.params['company_id'],
			countDownDesc: '', // 倒计时描述
			changeFieldLen: 0,
			changeFieldLen1: 0,
			changeFieldLen2: 0,
			changeFieldLen3: 0,
			changeFieldLen4: 0,
			couldBeCreated: false,
			changeTimes:0, // 首次change事件
			changeTimesUser: 0, // 客户采购分析change事件
			userBasicInfoArr: [
				'name',
				'sex',
				'age',
				'city',
				'wechat_code',
				'add_phone',
				'add_flag',
				'mobile',
				'mobile1',
				'phone',
				'profession_id',
				'job_id',
				'specialty',
				'conf_role',
				'company_role',
				'recommand_name',
				'recommand_mobile',
				'recommand_mobile1',
				'recommand_phone',
				'comment'
			],
			orguserbasicinfo:{},//原始的6项比较数据
			destuserbasicinfo:{},//动态更改的6项比较数据
			//本地存储自定义标签数据
			userDefinedData: {
				profession: [],
				has_other_req: [],
        personal_tag: [],
        department: [],
        job:[]
			},
			userbasicRule:[
        {
          name: "age",
          rule: /^[1-9][0-9]$|^[1-9]$/,
          desc: "年龄范围为1-99"
        },
				{
					name: "mobile",
					rule: /^1[345678]\d{9}$/,
					desc: "手机码号1格式不正确"
				},
				{
					name: "mobile1",
					rule: /^1[345678]\d{9}$/,
					desc: "手机码号2格式不正确"
				},
				{
					name: "recommand_mobile",
					rule: /^1[345678]\d{9}$/,
					desc: "推荐人手机码号1格式不正确"
				},
				{
					name: "recommand_mobile1",
					rule: /^1[345678]\d{9}$/,
					desc: "推荐人手机码号2格式不正确"
				},
        {
          name: "recommand_phone",
          rule: /^0\d{2,3}-?\d{7,8}$/,
          desc: "推荐人固定电话号码格式不正确"
        }
			],
			reqRule:[
				{
					name: "prev_year_budget",
					rule: /^[1-9]([0-9]{0,3}|[0-0]{4})$/,
					desc: "会议总预算必须为正整数且不能大于10000万"
				},
				{
					name: "current_year_num",
					rule:  /^[0-9]+$/,
					desc: "会议总次数必须为正整数"
				},
				{
					name: "hxe_percentage",
					rule: /^([0-9]\d?(\.\d{1,2})?|0.\d{1,2}|100)$/,
					desc: "通过小二预定占比必须为小于100的正整数"
				},
				{
					name: "next_conf_info", // 带原因的字段单独处理
				},
			],
			consumptionRule:{}, // 消费区间的规则
			userfeatureRule:[

				{
					name: "percentage",
					rule: /^(0|([1-9][0-9]?)|(100))(\.\d{0,2})?$/,
					desc: "房餐会的比例必须为小于等于100的数"
				},
				{
					name: "conf_percentage",
					rule: /^(0|([1-9][0-9]?)|(100))(\.\d{0,2})?$/,
					desc: "纯会的比例必须为小于100的正整数"
				},
				{
					name: "num",
					rule:  /^[1-9][0-9]*$/,
					desc: "房餐会次数必须为整数且大于0"
				},
				{
					name: "price",
					rule:  /^[1-9]([0-9]{0,3}|[0-0]{4})$/,
					desc: "房餐会平均每场金额必须为正整数且不能大于10000万"
				},
				{
					name: "participant", // 带原因的字段单独处理
				},
				{
					name: "often_conf_area", // 带原因的字段单独处理
				},
				{
					name: "decision", // 带原因的字段单独处理
				},
			],
			//以下是提交的数据
			userbasicinfo: {
        mobile: "",
        mobile1: "",
        name: "",
        sex: "",
        age: "",
        city: "",
        city_name: "",
        add_phone: "",
        add_flag: "",
        wechat_code: "",
        phone: "",
        profession_id: "",
        job_id: "",
        conf_role: "",
        company_role: "",
        specialty: "",
        comment: "",
        recommand_id: "",
        recommand_name: "",
        recommand_mobile: "",
        recommand_mobile1: "",
        recommand_phone: "",
        recommand_new_mobile: "",
        recommand_company_id: ""
      }
      ,
			reqinfo: {},
			userfeature:{
			},
			companyfeature:{
			}
		}
	},
	created(){
		let self = this;
		self.company_id = self.$route.params['company_id'];
		self.mobile = self.$route.params['mobile'];
		self.getBasicData();
		self.getCustomerStatus();
		if (self.status == 1){
			self.setlocalData(); // 使用缓存给reqinfo、userfeature、companyfeature赋值
		}
	},
	watch: {
	    '$route' (to, from) {
        let self = this;
				self.resetData();
        self.company_id = self.$route.params['company_id'];
				self.mobile = self.$route.params['mobile'];
				self.getBasicData();
				self.getCustomerStatus();
				if (self.status == 1){
					self.setlocalData(); // 使用缓存给reqinfo、userfeature、companyfeature赋值
				}
	    }
	},
	methods: {
		setlocalData() {
			let self = this;

			let storageId = self.storageId // 缓存id
			if (localStorage.getItem(storageId)) {
				let localData = JSON.parse(localStorage.getItem(storageId))
				if (localData['reqinfo']) {
					for (let prop in localData['reqinfo']){
							self.reqinfo =  localData['reqinfo'];
							break;
					 }
				}
				if (localData['userfeature']) {
					for (let prop in localData['userfeature']){
							self.userfeature =  localData['userfeature'];
							break;
					 }
				}

				if (localData['companyfeature']) {
					for (let prop in localData['companyfeature']){
							self.companyfeature =  localData['companyfeature'];
							break;
					 }
				}
			}
		},
		resetData(){
			let self = this;
			self.changeFieldLen = 0;
			self.changeFieldLen1 = 0;
			self.changeFieldLen2 = 0;
			self.changeFieldLen3 = 0;
			self.changeFieldLen4 = 0;
			self.couldBeCreated = false;
			self.userbasicinfo = {
        mobile: "",
        mobile1: "",
        name: "",
        sex: "",
        age: "",
        city: "",
        city_name: "",
        add_phone: "",
        add_flag: "",
        wechat_code: "",
        phone: "",
        profession_id: "",
        job_id: "",
        conf_role: "",
        company_role: "",
        specialty: "",
        comment: "",
        recommand_id: "",
        recommand_name: "",
        recommand_mobile: "",
        recommand_mobile1: "",
        recommand_phone: "",
        recommand_new_mobile: "",
        recommand_company_id: ""
      }
      ;
			self.reqinfo = {};
			self.userfeature = {};
			self.companyfeature = {};
			self.orguserbasicinfo = {};
			self.destuserbasicinfo = {};
			self.changeTimes = 0;
			self.changeTimesUser = 0;
			self.creating = false;
			self.city = [];
			self.area = [];
			self.userDefinedData =  {
				profession: [],
				has_other_req: [],
            	personal_tag: [],
            	department: []
			};
			self.storageId =  'draft_' + self.$route.params['mobile'] + self.$route.params['company_id']
		},
		setTimeCountDown(type) {
			let self = this;
			clearInterval(self.timer);
		    self.timer = setInterval(() => {

		      --self.time;
		      if(self.time == 0) {
							if (type == 1) {
								self.countDownDesc = "您未在一个小时内完成录入，请稍后从新进入完成录入";
							} else {
								self.countDownDesc = "您现在可以编辑了，请在客户列表点击客户开始编辑。";
							}

		          clearInterval(self.timer);
		          return;
		      }
		      let minutes = Math.floor(self.time/60);
		      let seconds =  Math.floor(self.time - minutes*60);
					if (type == 1) {
						self.countDownDesc = '完成创建倒计时: '+minutes+"分 "+seconds+"秒";
					} else {
						self.countDownDesc = '此客户正在有其他同事在编辑，如果在'+minutes+"分 "+seconds+"秒后，此同事未完成编辑，您才可以进行编辑";
					}
		    },1000)
		},
		getCustomerStatus(){
			let self = this;
			self.$ajax.post(
				self.$api.getModuleHost('CRM', "customer/getCustomerStatus"),
				{
					company_id: self.company_id,
					mobile: self.mobile
				}
			)
			.then(function(data){
				self.$emit('querySuc')
				if(data.code == 1000){
					if (data.data.authority == false) {
						self.creating = true;
						self.countDownDesc = data.data.message;
						return;
					}
					self.time = data.data.time;
					if(data.data.status == 1){
						self.setTimeCountDown(1);
						self.creating = false;
					} else {
						self.setTimeCountDown(2);
						self.creating = true;
					}
          self.$emit('setCustomerStatusId',data.data.id)
				}
			})
			.catch(function (err) {
				console.error(err);
			});
		},
		getBasicData(){
			let self = this;
			self.$ajax.get(
				self.$api.getModuleHost('CRM', "customer/getBasicData")
			)
			.then(function (data) {
				if (data.code == 1000) {
					self.baseData = data.data;
					//经过测试，决策快慢的原因如果不用新的对象进行重写，则会报错
					self.baseData.decision_reason_fast = self.baseData.decision_reason[0];
					self.baseData.decision_reason_slow = self.baseData.decision_reason[1];

					if(self.city.length){
						self.city = [];
					}
					if(self.area.length){
						self.area = [];
					}
					//给消费区间添加验证规则
					for(let item of self.baseData.consumption){
						self.consumptionRule['cons_'+item.id] = "会议消费区间"+item.name+"必须为正整数";
					}

					for(let itemCity of self.baseData.city){
						let obj = {
							id: itemCity.id,
							name: itemCity.name,
							disabledOften: false,
							disabledOccasionally: false
						};
						self.city.push(obj);
					}
					if (self.status == 1){
						self.$refs.part1.disabledCity('', self.city);
					}

					for(let itemArea of self.baseData.area){
						let obj = {
							id: itemArea.id,
							name: itemArea.name,
							selected: false,
							comment: ""
						};

						self.area.push(obj);
					}

					// 自定义的基础数据如果存于缓存中，则加入基础数据
					let ls = JSON.parse(localStorage.getItem(self.storageId));
					if (ls) {
						let userDefined = ls['userDefinedData']
						if(userDefined) {
							let profession = userDefined['profession']; // 自定义职业
							if (profession) {
								for (let k in profession) {
										self.baseData['profession'].push({'id': profession[k]['id'], 'name': profession[k]['name']})
								}
							}

							let otherReq = userDefined['has_other_req']; // 其他会务需求
							if (otherReq) {
								for (let k in otherReq) {
										self.baseData['has_other_req'].push({'id': otherReq[k]['id'], 'name': otherReq[k]['name']})
								}
							}

							let personal_tag = userDefined['personal_tag']; // 客户个性标签
							if (personal_tag) {
								for (let k in personal_tag) {
										self.baseData['personal_tag'].push({'id': personal_tag[k]['id'], 'name': personal_tag[k]['name']})
								}
							}

							let department = userDefined['department']; // 公司常办会部门
							if (department) {
								for (let k in department) {
										self.baseData['department'].push({'id': department[k]['id'], 'name': department[k]['name']})
								}
							}
						}
					}
				}
			}).catch(function (err) {
				console.error(err);
			});
		},
		setUserDefined(opt){
			let self = this;
			let pushed = false;
			if(self.baseData[opt.type_name].length){
				for(let item of self.baseData[opt.type_name]){
					if(item.id == opt.id){
						pushed = true;
					}
				}
			}
			if(!pushed){
				self.baseData[opt.type_name].push({
					id: opt.id,
					name: opt.name
				});
				self.userDefinedData[opt.type_name].push({
					id: opt.id,
					name: opt.name
				});
			}
			self.saveLocalData();
		},
		setUserBasicInfoAll(opt){
			let self = this;
			self.userbasicinfo = opt;
			for(let item of self.userBasicInfoArr){
				self.orguserbasicinfo[item] = opt[item];
			}
			for(let item of self.userBasicInfoArr){
				self.destuserbasicinfo[item] = opt[item];
			}

		},
		setUserBasicInfo(opt){

			let self = this;
			self.changeTimes++;
			//let storageId = 'draft_' + self.mobile + self.company_id // 缓存id
			//if (!localStorage.getItem(storageId)) {
				self.userbasicinfo[opt[0]] = opt[1];
			//}
			self.destuserbasicinfo[opt[0]] = opt[1]; // 给用来对比的数据添加值
			self.changeFieldLen1 = 0;
			for(let item in self.orguserbasicinfo){ // 判断基础信息的改变次数
				for(let item1 in self.destuserbasicinfo){
					if (item == item1){
						if(self.orguserbasicinfo[item] != self.destuserbasicinfo[item1]){
							self.changeFieldLen1++;
						}
					}
				}
			}

			// 设置按钮可编辑状态
			self.setCouldBeCreated();

			if (self.changeFieldLen1 > 0) { // 如果改变项大于0则存储本地缓存
				self.saveLocalData();
			} else {
				if(self.changeTimes > 12) { // 6为第一个基础数据默认change事件的个数[该事件是由baseInfoEdit blur事件控制的]
					localStorage.removeItem(self.storageId)
				}
			}

		},
		setCouldBeCreated(){
			let self = this;
			self.changeFieldLen = self.changeFieldLen1 + self.changeFieldLen2 + self.changeFieldLen3 + self.changeFieldLen4;
			if(self.changeFieldLen>=6){
				self.couldBeCreated = true;
			} else {
				self.couldBeCreated = false;
			}
		},
		setReqInfo(opt){
			let self = this;
			self.changeTimesUser++;
			self.reqinfo[opt[0]] = opt[1];
			self.changeFieldLen2 = 0;
			//  计算非空值以此来判断是否满足6项数据可以编辑
			for (let k  in self.reqinfo) {
				if (typeof self.reqinfo[k] == 'object') {
					if (k == "allopatry_conf_info" || k == 'consumption_interval') { // 跨地区举办会议、会议消费区间格式特殊 单独统计
						for (let y in self.reqinfo[k]) {
							if (self.reqinfo[k][y]['num'] != '') {
								self.changeFieldLen2++;
							}
							if (k == 'allopatry_conf_info'){
								if (self.reqinfo[k][y]['id'] > 0) {
									self.changeFieldLen2++;
								}

								if (self.reqinfo[k][y]['price'] != '') {
									self.changeFieldLen2++;
								}
							}
						}
					} else {
						for (let y in self.reqinfo[k]) {

							if (self.reqinfo[k][y]) {
								self.changeFieldLen2++;
							}
						}
					}
				} else {
					if (self.reqinfo[k] != '' && k != 'reason') { //把原因摆出在外
						self.changeFieldLen2++;
					}
				}
			}
			self.setCouldBeCreated();

			if (self.changeFieldLen2 > 0) { // 如果改变项大于0则存储本地缓存
					self.saveLocalData();
			}
		},
		setUserFeature(opt){
			let self = this;
			self.userfeature[opt[0]] = opt[1];
			self.changeFieldLen3 = 0;
			//  计算非空值以此来判断是否满足6项数据可以编辑

			for (let k  in self.userfeature) {
				if (typeof self.userfeature[k] == 'object') {
					for (let y in self.userfeature[k]) {
						if (self.userfeature[k][y]) {
							self.changeFieldLen3++;
						}
					}
				} else {
					if (self.userfeature[k] != '') {
						self.changeFieldLen3++;
					}
				}
			}
			self.setCouldBeCreated();
			if (self.changeFieldLen3 > 0  && self.changeTimesUser > 1) { // 如果改变项大于0则存储本地缓存
					self.saveLocalData();
			}
		},
		setCompanyFeature(opt){
			let self = this;
			self.companyfeature[opt[0]] = opt[1];
			self.changeFieldLen4 = 0;
			//  计算非空值以此来判断是否满足6项数据可以编辑

			for (let k  in self.companyfeature) {

				if (typeof self.companyfeature[k] == 'object') {
					for (let y in self.companyfeature[k]) {
						if (self.companyfeature[k][y]) {
							self.changeFieldLen4++;
						}
					}
				} else {
					if (self.companyfeature[k] != '') {
						self.changeFieldLen4++;
					}
				}
			}
			self.setCouldBeCreated();
			if (self.changeFieldLen4 > 0) { // 如果改变项大于0则存储本地缓存
					self.saveLocalData();
			}
		},
		sixLimited() {
			let self = this;
			self.$message({
				message: '您至少需要完善6项客户信息，才能完成创建。',
				type: 'warning',
				duration: 1000
			});
		},
		createUser(){
			let self = this;
      self.$root.Bus.$emit('baseFormValidate');
      self.$root.Bus.$emit('formOneValidate');
      self.$root.Bus.$emit('formTwoValidate');
      self.$root.Bus.$emit('formThreeValidate');
			var validateMsg = self.validateForm()
			if (validateMsg !=""){
				self.$message({
          message: validateMsg,
          type: 'warning',
					duration: 1000
        });
				return;
			}

      self.userbasicinfo.require_mobile=self.mobile
      let phoneReg = /^0\d{2,3}-?\d{7,8}$/;
      let mobileReg = /^1[34578]\d{9}$/;
      if (self.companyfeature.decision_user){
        if (!mobileReg.test(self.companyfeature.decision_user.phone)
          &&!phoneReg.test(self.companyfeature.decision_user.phone)
          &&self.companyfeature.decision_user.phone!=''){
          self.$message({
            message: "决策人联系方式必须为手机或固定电话",
            type: 'warning',
            duration: 1000
          });
          return false;
        }
      }
      if (self.isCreating)
        return;
      self.isCreating=true;

			self.$ajax.post(
				self.$api.getModuleHost('CRM', "customer/saveCustomer"),
				{
					customer_id: self.customer_id,
					company_id: self.company_id,
					mobile: self.mobile,
					userbasicinfo: JSON.stringify(self.userbasicinfo),
					reqinfo: JSON.stringify(self.reqinfo),
					userfeature: JSON.stringify(self.userfeature),
					companyfeature: JSON.stringify(self.companyfeature)
				}
			)
			.then(function (data) {
				if (data.code == 1000) {
					localStorage.removeItem(self.storageId);
					self.$root.Bus.$emit('addSuc',[self.mobile,self.company_id]);
					self.$bus.$emit('flashUserList')
					self.$router.push({
						path: '/foreground/index/userview/' + self.mobile + '/' + data.company_id + '/' + data.customer_id
					});
				} else {
					alert(data.msg);
				}
        self.isCreating=false;
			}).catch(function (err) {
				console.error(err);
        self.isCreating=false;
			});
		},
		validateForm() {

			let ruleDesc = "";
			let self = this;
			for (let k in self.userbasicRule){ // 验证基础数据中的格式
				let ruleName = self.userbasicRule[k]['name']

				if(self.userbasicinfo[ruleName]){
					let rule = self.userbasicRule[k]['rule'];
					if (!rule.test(self.userbasicinfo[ruleName])){
						ruleDesc = self.userbasicRule[k]['desc'];
						return ruleDesc;
					}
				}
			}
			for (let k in self.reqRule){ // 验证需求信息中的格式
				let ruleName = self.reqRule[k]['name'] // 验证的字段名称

				if(self.reqinfo[ruleName]){ // 如果验证字段名称存在
					if(ruleName == "next_conf_info") {
						if (!self.reqinfo['reason']) {
							return ruleDesc = (self.baseData.year + 1)+"年会议总预算的原因不能为空";
						}
					} else {
						let rule = self.reqRule[k]['rule'];
						if (!rule.test(self.reqinfo[ruleName])){
							ruleDesc = self.reqRule[k]['desc'];
							return ruleDesc;
						}
					}
				}
			}
			// 消费区间单独验证
			if (self.reqinfo['consumption_interval']) {
				for (let k in self.reqinfo['consumption_interval']) {
				  if (!self.reqinfo['consumption_interval'][k]['num'])
            self.reqinfo['consumption_interval'][k]['num']=''
					var num = self.reqinfo['consumption_interval'][k]['num']
					var id = self.reqinfo['consumption_interval'][k]['id']
					if (num != '') {
					  console.log(self.reqinfo['consumption_interval'])
						if (!/^[0-9]+$/.test(num)) {
							ruleDesc = self.consumptionRule['cons_'+id];
							return ruleDesc;
						}
					}
				}
			}
			// 常跨地区举办的两种会议单独验证
			if (self.reqinfo['allopatry_conf_info']) {
				for (let k in self.reqinfo['allopatry_conf_info']) {
					var num = self.reqinfo['allopatry_conf_info'][k]['num']
					var price = self.reqinfo['allopatry_conf_info'][k]['price']
					if (price) {
						if (!/^[0-9]+$/.test(price)) {
							ruleDesc = "常跨地区举办的两种会议金额必须正整数"
							return ruleDesc;
						}
					}
					if (num) {
						if (!/^[0-9]+$/.test(num)) {
							ruleDesc = "常跨地区举办的两种会议次数必须正整数"
							return ruleDesc;
						}
					}
				}
			}
			for (let k in self.userfeatureRule){ // 验证用户特性中的格式
				let ruleName = self.userfeatureRule[k]['name'];

				if (ruleName == 'participant' || ruleName=="often_conf_area" || ruleName=="decision") {
					//console.log(ruleName);
					//return;

					if (ruleName == 'often_conf_area') { // 经常使用区域的判断
						for (let i in self.often_conf_area){
							if (self.often_conf_area[i]['selected']) {

								if (!self.often_conf_area[i]['comment']) {
									return ruleDesc = self.often_conf_area[i]['name']+"的原因不能为空";
								}
							}
						}
					}

          if(self.userfeature.decision == 2){
            self.userfeature.decision_reason = 0;
          }

          if (ruleName == 'decision') { // 决策原因不能为空
						if(self.userfeature[ruleName]){
							if (self.userfeature['decision']!=2&&!self.userfeature['decision_reason']) {
								return ruleDesc = "决策原因不能为空";
							}
						}
					}
					if (ruleName == 'participant') { // 客户敏感度
						if(self.userfeature[ruleName]){
							if (self.userfeature[ruleName][0]['comment'] == '' && self.userfeature[ruleName][0]['id'] != '') {
								return ruleDesc = "客户价格敏感度的原因不能为空";
							}
						}
					}


				}
        else if(ruleName=='num'||ruleName=='price'){
          let rule = self.userfeatureRule[k]['rule'];
          if(self.userfeature['percentage']
            &&self.userfeature['percentage'][ruleName] != "") {
            if (!rule.test(self.userfeature['percentage'][ruleName])){
              ruleDesc = self.userfeatureRule[k]['desc'];
              return ruleDesc;
            }
          }
        }
				else {

					if (self.userfeature[ruleName]) {
						let rule = self.userfeatureRule[k]['rule'];
						if(self.userfeature['percentage'][ruleName] != "") {
							if (!rule.test(self.userfeature['percentage'][ruleName])){
								ruleDesc = self.userfeatureRule[k]['desc'];
								return ruleDesc;
							}
						}
					}
				}
			}

			// userbasicinfo
			// reqinfo
			// userfeature
			// companyfeature

			return ruleDesc;
		},
		saveLocalData(){
			let self = this;
			let id = 'draft_' + self.mobile + self.company_id;
			let data= {};
			data = {
				userbasicinfo: self.userbasicinfo,
				reqinfo: self.reqinfo,
				userfeature: self.userfeature,
				companyfeature: self.companyfeature,
				userDefinedData: self.userDefinedData
			};
//			本地缓存数据异常处理
      if (!!data.reqinfo.consumption_interval){
        for (let i = 0 ; i<data.reqinfo.consumption_interval.length;i++){
          if (!data.reqinfo.consumption_interval[i].num){
            data.reqinfo.consumption_interval[i].num = ""
          }
        }
      }
			localStorage.setItem(id, JSON.stringify(data));
		},
		testData(){
			let self = this;
			let data = {
					customer_id: self.customer_id,
					company_id: self.company_id,
					userbasicinfo: self.userbasicinfo,
					reqinfo: self.reqinfo,
					userfeature: self.userfeature,
					companyfeature: self.companyfeature
				};
			//console.log(JSON.stringify(data));
		}
	},
	computed: {

	},
	components: {
		baseInfoEdit,
		requirementEditPart1,
		requirementEditPart2,
		requirementEditPart3
	}
}
</script>
