		margin-bottom: 20px;
		height: 0;
		border-bottom: 1px solid #dcdcdc;
	}
	h2{
		margin: 10px 0;
	}
  .header-btn{
    position:absolute;
    right:30px;
    top:4px;
  }

	.el-input__inner{
		height: 28px;
		line-height: 28px;
	}
	.el-input{
		width: 80%;
    .el-icon-loading{
      display:none;
    }
	}
	.el-textarea{
		width: 80%;
	}
	.el-textarea textarea{
		resize: none;
	}
	.media-input .el-input{
		margin-right: 5px;
		width: 100px;
	}
	.text-align-left .el-form-item__label{
		text-align: left;
