	.el-checkbox__label,.el-radio__label{
		margin-left: 0;
		padding-left: 0
	}
	.el-checkbox+.el-checkbox,.el-radio+.el-radio{
		margin-left: 0;
	}
	.el-checkbox,.el-radio{
		margin-left: 0;
		margin-right: 10px;
	}

	//添加新数据
	.add-data{
		color: #20a0ff;
    span{
      cursor:pointer;
    }
	}
  .createTip{
    font-size:12px;
    font-weight:normal;
    color:#FF4949;
  }
	.count-down-wrap{
    position:absolute;
