<template>
	<el-form :model="orguserfeature" :rules="editTwoRules" ref="form3" label-width="140px">
	 	<el-row justify="space-around">
			<el-form-item label="偏好场地类型">
				<el-checkbox-group
					v-model="orguserfeature.site_type"
					@change="setArrToString('site_type')">
					<el-checkbox
						v-for="item in baseData.banquet_type"
						:label="item.id+''">
						{{item.name}}

					</el-checkbox>
				</el-checkbox-group>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="偏好调性">
				<el-checkbox-group
					v-model="orguserfeature.preference"
					@change="setArrToString('preference')">
					<el-checkbox
						v-for="item in baseData.style_feature"
						:label="item.id+''">
						{{item.name}}

					</el-checkbox>
				</el-checkbox-group>
			</el-form-item>
		</el-row>
		<el-row justify="space-around">
			<el-form-item label="经常使用场地区域">
				<el-row>
					<el-checkbox
            v-for="item in often_conf_area"
						:checked="Boolean(item.selected)"
						v-model="item.selected"
						@change="setOftenConfArea()">
						{{item.name}}
					</el-checkbox>
				</el-row>
				<el-row>
				</el-row>
			</el-form-item>
      <el-row class="relative">
        <el-form-item v-show="item.selected" v-for="item in often_conf_area" :label="item.name" >
          <el-input
            placeholder="原因"
            class="margin-bottom10"
            v-model.trim="item.comment"
            @blur="setOftenConfArea()">
          </el-input>
          <div class="el-form-item__error reasonError" :class="{errorShow:item.comment==''}">请输入原因</div>
        </el-form-item>
      </el-row>
		</el-row>
		<el-row justify="space-around">
			<el-form-item label="决策快慢">
				<el-row>
					<el-col :span="8">
						<el-select placeholder="请选择决策速度"
							v-model="orguserfeature.decision"
							@change="resetFiledDecisionReason();">
							<el-option
								label="请选择"
								:value="0">
							</el-option>
						  	<el-option
								v-for="item in decisionSpeed_arr"
								:label="item.name"
								:value="item.id"
								>
						    </el-option>
					  	</el-select>
					</el-col>
					<el-col :span="8" v-show="orguserfeature.decision == 1">
            <el-form-item prop="decision_reason">
              <el-select placeholder="请选择决策快的原因"
                v-model="orguserfeature.decision_reason"
                @change="changeField('decision_reason',orguserfeature.decision_reason)">
                <el-option
                  label="请选择原因"
                  :value="0">
                </el-option>
                <el-option
                  v-for="item in baseData.decision_reason_fast"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
					</el-col>
					<el-col :span="8" v-show="orguserfeature.decision == 3">
            <el-form-item prop="decision_reason">
						<el-select placeholder="请选择决策慢的原因"
							v-model="orguserfeature.decision_reason"
							@change="changeField('decision_reason',orguserfeature.decision_reason)">
							<el-option
								label="请选择原因"
								:value="0">
							</el-option>
						  	<el-option
								v-for="item in baseData.decision_reason_slow"
								:label="item.name"
								:value="item.id">
						    </el-option>
						</el-select>
            </el-form-item>
          </el-col>
				</el-row>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="客户价格敏感度" >
				<el-row>
					<el-radio-group
						v-model="orguserfeature.participant[0].id"
						@change="setParticipantComment()">
					  	<el-radio
					  		v-for="item in baseData.price_sensitive"
					  		:label="item.id">
					  		{{item.name}}
					  	</el-radio>
					</el-radio-group>
				</el-row>
				<el-row v-show="orguserfeature.participant[0].id!=0">
          <el-form-item prop="participant[0].comment">
					<el-input  type="textarea" placeholder="原因"
						v-model="orguserfeature.participant[0].comment"
						@blur="setParticipant()">
					</el-input>
          </el-form-item>
				</el-row>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="房餐会次数占比">
				<el-row>
					<el-col :span="6">
						<el-form-item label="房餐会" class="text-align-left">

						</el-form-item>
						<el-input
							v-model="orguserfeature.percentage.percentage"
							@blur="setPercentageNum();">
						</el-input> %
					</el-col>
					<el-col :span="6">
						<el-form-item label="纯会、餐会、房会" class="text-align-left">

						</el-form-item>
						<el-input
							:disabled="true"
							v-model="conf_percentage">
						</el-input> %
					</el-col>
				</el-row>
				<el-row v-show="orguserfeature.percentage.percentage!=''&&orguserfeature.percentage.percentage!='0'&&orguserfeature.percentage.percentage!='0.00'">
					<el-col :span="6">
						<el-form-item label="房餐会次数" class="text-align-left">

						</el-form-item>
						<el-input
							v-model="orguserfeature.percentage.num"
							@blur="setPercentageField('num')">
						</el-input> 次

					</el-col>
					<el-col  :span="6">
						<el-form-item label="房餐会平均每场" class="text-align-left">
						</el-form-item>
						<el-input
							v-model="orguserfeature.percentage.price"
							@blur="setPercentageField('price')">
						</el-input> 万
            <div class="el-form-item__error " v-show="orguserfeature.percentage.price!=''
            &&orguserfeature.percentage.price>10000">房餐会平均每场必须小于10000万</div>
            <div class="el-form-item__error " v-show="
            !(orguserfeature.percentage.price!=''
            &&orguserfeature.percentage.price>10000)
            &&orguserfeature.percentage.num!=''&&
           orguserfeature.percentage.num<=0">房参会次数必须大于0</div>
					</el-col>
				</el-row>

			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="是否有策划搭建需求">
				<el-row>
					<el-radio-group
						v-model="orguserfeature.build_require"
						@change="resetBuildRequireType()">
						<el-radio
					  		v-for="item in establishReq_arr"
					  		:label="item.id">
					  		{{item.name}}
					  	</el-radio>
					</el-radio-group>
				</el-row>

				<el-row v-if="!hideBuildRequireType">
					<el-checkbox-group
						v-model="orguserfeature.build_require_type"
						@change="setArrToString('build_require_type');">
						<el-checkbox
							v-for="item in baseData.has_other_req"
							:label="item.id+''"
							>
						  	{{item.name}}
						</el-checkbox>
				  	</el-checkbox-group>
				  	<p class="add-data">
					<span @click="showAddArea('BuildReq')">
            <i  :class="{'el-icon-minus':showBuildReqUserDef,'el-icon-plus':!showBuildReqUserDef}"></i>
            {{showBuildReqUserDef==true?' 收起':' '}}添加其余会务需求
          </span>
              <el-row>
                <el-col :span="12">
                  <tagAdder
                    v-if="showBuildReqUserDef"
                    :tagData="tagData.has_other_req"
                    @getUserDefined="setUserDefined">
                  </tagAdder>
                </el-col>
              </el-row>
					</p>
				</el-row>

			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="客户特质">
				<el-row>
					<el-radio-group
						v-model="orguserfeature.trait"
						@change="changeField('trait',orguserfeature.trait)">
						<el-radio
							v-for="item in baseData.personal_feature"
							:label="item.id">
							{{item.name}}

						</el-radio>
				  	</el-radio-group>
				</el-row>
			</el-form-item>
		</el-row>

		<el-row justify="space-around">
			<el-form-item label="客户个性标签">
				<el-row>
					<el-checkbox-group v-model="orguserfeature.customer_tag">
						<el-checkbox
							v-for="item in baseData.personal_tag"
							:label="item.id+''"
							@change="setArrToString('customer_tag')">
							{{item.name}}

						</el-checkbox>
				  	</el-checkbox-group>
				  	<p class="add-data">
						<span @click="showAddArea('PersonalTag');">
              <i  :class="{'el-icon-minus':showPersonalTagUserDef,'el-icon-plus':!showPersonalTagUserDef}"></i>
              {{showPersonalTagUserDef==true?' 收起':' '}}添加自定义客户个性标签
            </span>
					</p>
				</el-row>
				<el-row>
					<el-col :span="12">
						<tagAdder
							v-if="showPersonalTagUserDef"
							:tagData="tagData.personal_tag"
							@getUserDefined="setUserDefined">
						</tagAdder>
					</el-col>
				</el-row>
			</el-form-item>
		</el-row>

		<el-button v-if="status == 2"
			type="primary"
			class="margin-bottom10"
			@click="save();" :disabled="isInSave">
			保存
		</el-button>
	</el-form>
</template>

<script>

import tagAdder from '../common/tagAdder.vue'
import ElFormItem from "../../../node_modules/element-ui/packages/form/src/form-item.vue";
import ElForm from "../../../node_modules/element-ui/packages/form/src/form.vue";
import ElRow from "element-ui/packages/row/src/row";

export default {
	props: [
		'status',
		'customer_id',
		'company_id',
		'mobile',
		'baseData',
		'area'
	],
	data: function () {
		let self = this;
		return {
      isInSave:false,
			orguserfeature:{
				decision: 0,
				decision_reason:0,
				build_require: '',
				trait: '',
				site_type: [],
				preference: [],
				build_require_type: [],
				customer_tag: [],
				often_conf_area: [],
				participant: [
					{
						id: '',
						comment: ''
					}
				],
				percentage: {
					percentage : '',
					num: '',
					price: ''
				}
			},
			userfeatureRule:[
				{
					name: "percentage",
					rule: /^(0|([1-9][0-9]?)|(100))(\.\d{0,2})?$/,
					desc: "房餐会的比例必须为小于等于100的数"
				},
				{
					name: "conf_percentage",
					rule: /^(0|([1-9][0-9]?)|(100))(\.\d{0,2})?$/,
					desc: "纯会的比例必须为小于100的正整数"
				},
				{
					name: "num",
					rule:  /^[1-9][0-9]*$/,
					desc: "房餐会次数必须为整数且大于0"
				},
				{
					name: "price",
					rule:  /^[1-9]([0-9]{0,3}|[0-0]{4})$/,
					desc: "房餐会平均每场金额必须为正整数且不能大于10000万"
				},
				{
					name: "participant", // 带原因的字段单独处理
				},
				{
					name: "often_conf_area", // 带原因的字段单独处理
				},
				{
					name: "decision", // 带原因的字段单独处理
				},
			],
			self_build_require : '',
			self_build_require_type : [],
			self_participant_id : '',
			self_participant_comment : '',
			conf_percentage: '',
			percentageZero: true,
			often_conf_area: self.area,
			decisionSpeed_arr: [
				{
					id: 1,
					name: "快"
				},
				{
					id: 2,
					name: "中"
				},
				{
					id: 3,
					name: "慢"
				}
			],
			establishReq_arr: [
            	{
                    id: 1,
                    name: "较多"
                },
                {
                    id: 2,
                    name: "偶尔"
                },
                {
                    id: 3,
                    name: "没有"
                },
            ],
            showBuildReqUserDef: false,
            showPersonalTagUserDef: false,
            tagData: {
            	'has_other_req':
            		{
            			tag: 'has_other_req',
            			tagName: '其余会务需求'
            		},
            	'personal_tag':
            		{
            			tag: 'personal_tag',
            			tagName: '自定义客户个性标签'
            		}
            },
            //以下是提交的数据
            userfeature:{},
            editTwoRules:{
              'participant[0].comment':[
                 { required: true, message: '请输入原因', trigger: 'blur' }
              ],
              'decision_reason':[
                {validator:(rule,value,callback)=>{
                  console.log(value)
                  if(value==0){
                    callback(new Error('请选择原因'));
                  }
                }}
              ]
            }

		}
	},
	created(){
		let self = this;
		if(self.status == 2){
			self.getOrgUserFeature();
		} else {
				self.setLocalData(); // 在创建页面的时候把本地数据赋值给双向绑定的数据
		}
    self.$root.Bus.$off('formTwoValidate')
    self.$root.Bus.$on('formTwoValidate',self.formTwoValidate)
	},
	watch: {
	    '$route' (to, from) {
	  		let self = this;
				self.resetData();
				if(self.status == 2){
					self.getOrgUserFeature();
				} else {
					self.setLocalData(); // 在创建页面的时候把本地数据赋值给双向绑定的数据
				}
	    }
	},
	methods: {
		resetData() {
			let self = this;
			self.orguserfeature = {
				decision: 0,
				decision_reason:0,
				build_require: '',
				trait: '',
				site_type: [],
				preference: [],
				build_require_type: [],
				customer_tag: [],
				often_conf_area: [],
				participant: [
					{
						id: 0,
						comment: ''
					}
				],
				percentage: {
					percentage : '',
					num: '',
					price: ''
				}
			}
			self.self_build_require = '';
			self.self_build_require_type = [];
			self.self_participant_id = '';
			self.self_participant_comment = '';
			self.conf_percentage = '';
			self.percentageZero = false;
			self.showBuildReqUserDef = false;
			self.showPersonalTagUserDef = false;
			self.userfeature = {};
		},
		setLocalData() {

			let self = this;
			// console.log(self.orguserfeature)
			// console.log(111111111111)
			let storageId = 'draft_' + self.mobile + self.company_id // 缓存id
			if (localStorage.getItem(storageId)) {
				let localData = JSON.parse(localStorage.getItem(storageId))
				localData = localData['userfeature'];
				for (let k in localData) {
					if (
							k == 'site_type' ||
							k == 'preference' ||
							k == 'build_require_type' ||
							k == 'customer_tag' ||
							k == 'often_conf_area'
						) { // 复选框的值是用逗号(‘,’)分割存储的，需要转成数组格式才能默认选中
						self.orguserfeature[k] = localData[k].split(',');
					} else {
						self.orguserfeature[k] = localData[k];
					}
				}
			}
		},
		showAddArea(tag){
			let self = this;
			self['show' + tag + 'UserDef'] = !self['show' + tag + 'UserDef'];
		},
		setUserDefined(opt){
			let self = this;
			self.$emit('getUserDefined',opt);
		},
		getOrgUserFeature(){
			let self = this;
			self.$ajax.get(
				self.$api.getModuleHost('CRM', "customer/userFeature"),
				{
					company_id: self.company_id,
					customer_id: self.customer_id
				}
			)
			.then(function (data) {
				if (data.code == 1000) {
					self.orguserfeature.decision = data.data.decision;
					self.orguserfeature.decision_reason = data.data.decision_reason;
					self.orguserfeature.build_require = data.data.build_require;
					self.self_build_require = data.data.build_require;
					self.orguserfeature.trait = data.data.trait;

					if(data.data.site_type!=''){
						self.orguserfeature.site_type = data.data.site_type.split(',');
					} else{
						self.orguserfeature.site_type = []
					}
					if(data.data.preference!=''){
						self.orguserfeature.preference = data.data.preference.split(',');
					} else{
						self.orguserfeature.preference = []
					}

					if(data.data.build_require_type!=''){
						self.orguserfeature.build_require_type = data.data.build_require_type.split(',');
						self.self_build_require_type = data.data.build_require_type.split(',');
					} else {
						self.orguserfeature.build_require_type = []
					}
					if(data.data.customer_tag!=''){
						self.orguserfeature.customer_tag = data.data.customer_tag.split(',');
					} else {
						self.orguserfeature.customer_tag = []
					}
					if(data.data.percentage!=''){
						self.orguserfeature.percentage = data.data.percentage;
						self.conf_percentage = 100 - parseFloat(self.orguserfeature.percentage.percentage);
            self.conf_percentage = self.conf_percentage
          } else {
						self.conf_percentage = '';
						self.orguserfeature.percentage =  {
								percentage : '',
								num: '',
								price: ''
							}
					}

					if(data.data.participant.length){
						self.orguserfeature.participant = data.data.participant;
						self.self_participant_id = data.data.participant[0].id;
						self.self_participant_comment = data.data.participant[0].comment;
					} else {
						self.orguserfeature.participant = [
								{
									id: '',
									comment: ''
								}
							]
					}

					self.orguserfeature.often_conf_area = data.data.often_conf_area;
					for(let itemOrg of self.often_conf_area){
            itemOrg.selected = false;
            itemOrg.comment = "";
						for(let item of self.orguserfeature.often_conf_area){
							if( item.id === itemOrg.id ){
								itemOrg.selected = true;
								itemOrg.comment = item.comment;
							}
						}
					}
				}
			}).catch(function (err) {
				console.error(err);
			});
		},
		changeField(feildName,feildVal){
    		let self = this;
    		if(self.status == 1){
    			self.$emit('getUserFeature',arguments);
    		} else {
    			self.userfeature[feildName] = feildVal;
    		}
    	},
    	resetBuildRequireType(){
    		let self = this;
    		self.orguserfeature.build_require_type = [];
			if(self.orguserfeature.build_require == self.self_build_require){
				self.orguserfeature.build_require_type = self.self_build_require_type;
				self.self_build_require = '';
				self.self_build_require_type = [];
			}
			self.changeField('build_require',self.orguserfeature.build_require)
			self.setArrToString('build_require_type');
    	},
		resetFiledDecisionReason(){
			let self = this;
			self.$refs['form3'].validate()
			if(self.orguserfeature.decision == 0){
				self.orguserfeature.decision_reason = 0;
			}

			if(self.orguserfeature.decision == 1){
				let bool = false;
				for(let item of self.baseData.decision_reason_fast){
					if(item.id == self.orguserfeature.decision_reason){
						bool = true;
						//return;
					}
				}
				self.orguserfeature.decision_reason = bool ? self.orguserfeature.decision_reason : 0;
			}

			if(self.orguserfeature.decision == 2){
				self.orguserfeature.decision_reason = 0;
			}

			if(self.orguserfeature.decision == 3){
				let bool = false;
				for(let item of self.baseData.decision_reason_slow){
					if(item.id == self.orguserfeature.decision_reason){
						bool = true;
						//return;
					}
				}
				self.orguserfeature.decision_reason = bool ? self.orguserfeature.decision_reason : 0;
			}

			self.changeField('decision',self.orguserfeature.decision);
		},
		setArrToString(str){
    		let self = this;
    		arguments[0] = str;
    		arguments[1] = self.orguserfeature[str].join(",");
    		if(self.status == 1){
    			self.$emit('getUserFeature',arguments);
    		} else {
    			self.userfeature[arguments[0]] = arguments[1];
    		}
    	},
    	setOftenConfArea(opt){
    		let self = this;
    		let self_often_conf_area = [];
    		for(let item of self.often_conf_area){
    			let obj = {
    				id: 0,
    				comment: ''
    			};
    			//if(item.selected && item.comment !== ''){
    			if(item.selected){
    				obj.id = item.id;
    				obj.comment = item.comment;
    				self_often_conf_area.push(obj);
    			} else {
    				item.comment = '';
    			}
    		}
    		arguments[0] = 'often_conf_area';
    		arguments[1] = self_often_conf_area;
    		if(self.status == 1){
    			self.$emit('getUserFeature',arguments);
    		} else {
    			self.userfeature[arguments[0]] = arguments[1];
    		}
    	},
    	setParticipantComment(){
    		let self = this;
    		self.orguserfeature.participant[0].comment = '';
    		if(self.orguserfeature.participant[0].id == self.self_participant_id){
    			self.orguserfeature.participant[0].comment = self.self_participant_comment;
    			self.self_participant_id = '';
    			self.self_participant_comment = '';
    		}
    		self.setParticipant();
    	},
    	setParticipant(){
    		let self = this;
    		arguments[0] = 'participant';
    		arguments[1] = self.orguserfeature.participant;
    		if(self.status == 1){
    			self.$emit('getUserFeature',arguments);
    		} else {
    			self.userfeature[arguments[0]] = arguments[1];
    		}
    	},
    	setPercentageNum(){											//设置“房餐会次数”和“房餐会平均每场”的显示和影藏，
    																//根据percentage是否为0和""进行判断
    		let self = this;
    		if(self.orguserfeature.percentage.percentage == 0){
					self.percentageZero = true;
					self.conf_percentage = 100 - self.orguserfeature.percentage.percentage;
	    		self.orguserfeature.percentage.num = '';
					self.orguserfeature.percentage.price = '';
    		}
    		if(self.orguserfeature.percentage.percentage == ''){
    			self.percentageZero = true;
    			self.conf_percentage = '';
    			self.orguserfeature.percentage.num = '';
				self.orguserfeature.percentage.price = '';
    		}
    		self.setPercentageField('percentage');
    	},
    	setPercentageField(str){							//用于判断“房餐会比例” “房餐会次数” “房餐会平均每场”的输入值是否合法
    		let self = this;
    		self.orguserfeature.percentage[str] = self.orguserfeature.percentage[str];		//字段值转为float型数据

    		// if(!self.orguserfeature.percentage[str] && self.orguserfeature.percentage[str] != 0){			//字段值不合法则清空
    		// 	self.orguserfeature.percentage[str] = '';
    		// 	self.percentageZero = false;
    		// }
    		if( ( self.orguserfeature.percentage.percentage > 0 ) && (self.orguserfeature.percentage.percentage <= 100) && ( self.orguserfeature.percentage.percentage != '' )){													//“房餐会比例”字段>0切<100并且非空时，计算纯会比例,
    			//并展示“房餐会次数” “房餐会平均每场”的输入域
    			self.percentageZero = false;
    			self.conf_percentage = 100 - self.orguserfeature.percentage.percentage;
          self.conf_percentage = self.conf_percentage.toFixed(2)
    		}

    		arguments[0] = 'percentage';											//设置房餐会数据
    		arguments[1] = self.orguserfeature.percentage;
    		if(self.status == 1){
    			self.$emit('getUserFeature',arguments);
    		} else {
    			self.userfeature[arguments[0]] = arguments[1];
    		}
    	},
    	setFormData(dataObj){
    		let self = this;
    		if(self.status == 1){
    			self.$emit('getUserFeature',arguments);
    		} else {
    			self.userfeature[arguments[0]] = arguments[1];
    		}
    	},
    	save(){
			let self = this;

			var validateMsg = self.validateForm()

			if (validateMsg !=""){
				self.$message({
          message: validateMsg,
          type: 'warning',
					duration: 1000
        });
				return;
			}
        if(self.userfeature.decision==2){
          self.userfeature.decision_reason=0;
        }
        if (self.isInSave)
          return
        self.isInSave=true;
			self.$ajax.post(
				self.$api.getModuleHost('CRM', "customer/saveCustomer"),
				{
					customer_id: self.customer_id,
					company_id: self.company_id,
					userfeature: JSON.stringify(self.userfeature)
				}
			)
			.then(function (data) {
				if (data.code == 1000) {
					self.$emit('getBool');
				} else {
					alert(data.msg);
				}
        self.isInSave=false;
			}).catch(function (err) {
				console.error(err);
        self.isInSave=false;
			});
		},
		validateForm() {
			let ruleDesc = "";

			let self = this;

			for (let k in self.userfeatureRule){ // 验证用户特性中的格式
				let ruleName = self.userfeatureRule[k]['name'];

				if (ruleName == 'participant' || ruleName=="often_conf_area" || ruleName=="decision") {
					//console.log(ruleName);
					//return;

					if (ruleName == 'often_conf_area') { // 经常使用区域的判断
						for (let i in self.often_conf_area){
							if (self.often_conf_area[i]['selected']) {

								if (!self.often_conf_area[i]['comment']) {
									return ruleDesc = self.often_conf_area[i]['name']+"的原因不能为空";
								}
							}
						}
					}
					if (ruleName == 'decision') { // 决策原因不能为空
						if(self.userfeature[ruleName]){
							if (!self.userfeature['decision_reason'] && self.userfeature[ruleName]!=2) {
								return ruleDesc = "决策原因不能为空";
							}
						}
					}
					if (ruleName == 'participant') { // 客户敏感度
						if(self.userfeature[ruleName]){
							if (self.userfeature[ruleName][0]['comment'] == '' && self.userfeature[ruleName][0]['id'] != '') {
								return ruleDesc = "客户价格敏感度的原因不能为空";
							}
						}
					}


				}
				  else if(ruleName=='num'||ruleName=='price'){
          let rule = self.userfeatureRule[k]['rule'];
            if(self.userfeature['percentage']
              &&self.userfeature['percentage'][ruleName] != "") {
              if (!rule.test(self.userfeature['percentage'][ruleName])){
                ruleDesc = self.userfeatureRule[k]['desc'];
                return ruleDesc;
              }
          }
          }else {
					if (self.userfeature[ruleName]) {

						let rule = self.userfeatureRule[k]['rule'];
						if(self.userfeature['percentage'][ruleName] != "") {
							if (!rule.test(self.userfeature['percentage'][ruleName])){
								ruleDesc = self.userfeatureRule[k]['desc'];
								return ruleDesc;
							}
						}
					}
				}
			}
			return ruleDesc;
		},
    formTwoValidate(){
      this.$refs['form3'].validate()
    }
	},
	computed: {
		hideBuildRequireType: function(){
			let self = this;
			if (self.orguserfeature.build_require==''){
			  return true
      }
			return (self.orguserfeature.build_require === 3);
		}
	},
	components: {
    ElRow,
    ElForm,
    ElFormItem,
    tagAdder
	}
}
</script>
<style lang="less">
	.el-rate{
		margin-top: 8px;
	}
  .reasonError{
    display:none;
    position:absolute;
    top:100%;
    margin-top:-10px;
  }
  .errorShow{
    display:block;
  }
  .relative{
    position:relative;
  }
</style>



