<template>

	<el-form ref="orgUserBasicInfo" :model="orgUserBasicInfo" :rules="baseInfoRules" label-width="90px">

		<el-row type="flex" class="row-bg" justify="space-between">
			<el-col :md="6">
				<el-form-item label="客户姓名">
					<el-input
						v-model="orgUserBasicInfo.name"
						@blur="changeField('name',orgUserBasicInfo.name)">
					</el-input>
				</el-form-item>
			</el-col>
			<el-col :md="6">
				<el-form-item label="性别" label-width="50px">
					<el-radio-group
						v-model="orgUserBasicInfo.sex"
						@change="changeField('sex',orgUserBasicInfo.sex)"
						>
						<el-radio
							v-for="item in sex_arr" :label="item.id"
							class="sex-radio"
							>
							{{item.name}}
						</el-radio>
					</el-radio-group>
				</el-form-item>
			</el-col>
			<el-col :md="6">
				<el-form-item label="年龄" label-width="50px" prop="age">
					<el-input
						v-model="orgUserBasicInfo.age"
						@blur="changeField('age',orgUserBasicInfo.age)"
          >
					</el-input>
				</el-form-item>
			</el-col>
			<el-col :md="6">
				<el-form-item label="居住城市" label-width="70px">
					<span v-if="!orgUserBasicInfo.city_name"
						@click="showBeingLivedCity" class="city-live red-text text-link">
						点击选择
					</span>
					<span v-else @click="showBeingLivedCity" class="city-live text-link">
						{{orgUserBasicInfo.city_name}}
					</span>
					<cityList
						v-show="show_being_lived_city"
						:city_data="city_data"
						@getCity="setCity"
						@hide="hideCityList()"
						>
					</cityList>
				</el-form-item>
			</el-col>
		</el-row>

		<el-row>
			<el-col :md="8">
				<el-form-item label="微信号">
					<el-input
						v-model="orgUserBasicInfo.wechat_code"
						@blur="changeField('wechat_code',orgUserBasicInfo.wechat_code)">
					</el-input>
				</el-form-item>
			</el-col>
			<el-col v-if="orgUserBasicInfo.wechat_code!=''" :md="5">
				<div class="margin-top8">
					<el-checkbox v-model="orgUserBasicInfo.add_phone"
						:true-label="Number(1)"
						:false-label="Number(0)"
						@change="changeField('add_phone',orgUserBasicInfo.add_phone)">
						是否已添加到工作手机
					</el-checkbox>
				</div>
			</el-col>
			<el-col  v-if="orgUserBasicInfo.wechat_code!=''"  :md="8">
				<div class="margin-top8">
					<el-checkbox v-model="orgUserBasicInfo.add_flag"
						:true-label="Number(1)"
						:false-label="Number(0)"
						@change="changeField('add_flag',orgUserBasicInfo.add_flag)">
						工作手机微信号是否已添加标注
					</el-checkbox>
				</div>
			</el-col>
		</el-row>

		<el-row justify="space-around">
			<el-col :span="8">
				<el-form-item label="手机号码1" prop="mobile">
							<el-input
								v-model="orgUserBasicInfo.mobile"
								@blur="changeField('mobile',orgUserBasicInfo.mobile)">
							</el-input>
				</el-form-item>
			</el-col>
			<el-col :span="8">
				<el-form-item label="手机号码2" prop="mobile1">
					<el-input
						v-model="orgUserBasicInfo.mobile1"
						@blur="changeField('mobile1',orgUserBasicInfo.mobile1)">
					</el-input>
				</el-form-item>
			</el-col>
			<el-col :span="8">
				<el-form-item label="固定号码" prop="phone">
					<el-input
						v-model="orgUserBasicInfo.phone"
						@blur="changeField('phone',orgUserBasicInfo.phone)">

					</el-input>
				</el-form-item>
			</el-col>
		</el-row>

		<el-row justify="space-around">
			<el-col :span="24">
				<el-form-item label="职业">
					<el-row>
						<el-radio-group
							v-model="orgUserBasicInfo.profession_id"
							@change="changeField('profession_id',orgUserBasicInfo.profession_id)"
							>
							<el-radio
								v-for="item in baseData.profession"
								:label="item.id">
								{{item.name}}
							</el-radio>
						</el-radio-group>
						<p class="add-data">
             <span @click="showAddArea('Profession');">
               <i  :class="{'el-icon-minus':showProfessionUserDef,'el-icon-plus':!showProfessionUserDef}"></i>
               {{showProfessionUserDef==true?' 收起':' '}}添加自定义职业
             </span>
            </p>
					</el-row>
					<el-row>
						<el-col :span="12">
							<tagAdder
								v-if="showProfessionUserDef"
								:tagData="tagData.profession"
								@getUserDefined="setUserDefined">
							</tagAdder>
						</el-col>
					</el-row>
				</el-form-item>
			</el-col>
		</el-row>

		<el-row justify="space-around">
			<el-col :span="24">
				<el-form-item label="职位">
					<el-radio-group
						v-model="orgUserBasicInfo.job_id"
						@change="changeField('job_id',orgUserBasicInfo.job_id)"
						>
						<el-radio v-for="item in baseData.job" :label="item.id">
							{{item.name}}
						</el-radio>
					</el-radio-group>
          <p class="add-data">
             <span @click="showAddArea('Job');">
               <i  :class="{'el-icon-minus':showJobUserDef,'el-icon-plus':!showJobUserDef}"></i>
               {{showJobUserDef==true?' 收起':' '}}添加自定义职位
             </span>
          </p>
          <el-row>
            <el-col :span="12">
              <tagAdder
                v-if="showJobUserDef"
                :tagData="tagData.job"
                @getUserDefined="setUserDefined">
              </tagAdder>
            </el-col>
          </el-row>
				</el-form-item>
			</el-col>
		</el-row>
		<el-row justify="space-around">
			<el-col :span="8">
				<el-form-item label="办会专业度">
					<el-radio-group
						v-model="orgUserBasicInfo.specialty"
						@change="changeField('specialty',orgUserBasicInfo.specialty)">
						<el-radio v-for="item in specialty_arr" :label="item.id" class="specialty-radio">
							{{item.name}}
						</el-radio>
					</el-radio-group>
				</el-form-item>
			</el-col>
			<el-col :span="8">
				<el-form-item label="办会角色">
					<el-select
						v-model="orgUserBasicInfo.conf_role"
						@change="changeField('conf_role',orgUserBasicInfo.conf_role)"
						>
						<el-option
							label="请选择"
							:value="0">
						</el-option>
						<el-option
							v-for="item in baseData.conf_role"
							:key="item.id"
							:label="item.name"
							:value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
			</el-col>
			<el-col :span="8">
				<el-form-item label="在公司角色">
					<el-select
						v-model="orgUserBasicInfo.company_role"
						@change="changeField('company_role',orgUserBasicInfo.company_role)">
						<el-option
							label="请选择"
							:value="0">
						</el-option>
						<el-option
							v-for="item in baseData.company_role"
							:key="item.id"
							:label="item.name"
							:value="item.id">
						</el-option>
					</el-select>
				</el-form-item>
			</el-col>
		</el-row>
    <el-row>
      <el-col :span="24">
        <el-form-item label="办会人备注">
          <el-input type="textarea"
            v-model="orgUserBasicInfo.comment"
            @blur="changeField('comment',orgUserBasicInfo.comment)"
          >
            {{orgUserBasicInfo.comment}}
          </el-input>
        </el-form-item>
      </el-col>
    </el-row>
		<h2>推荐人/友好关系人</h2>
		<el-row justify="space-around">
			<el-col :span="8">
			<el-form-item label="姓名">
				<el-input
					v-model="orgUserBasicInfo.recommand_name"
					@blur="changeField('recommand_name',orgUserBasicInfo.recommand_name)"
					>
				</el-input>
			  </el-form-item>
			</el-col>
		</el-row>
		<el-row justify="space-around">
			<el-col :span="8">
			<el-form-item label="手机号码1" prop="recommand_mobile">
				<el-input
					v-model="orgUserBasicInfo.recommand_mobile"
          @change="getRecommandInfo(orgUserBasicInfo.recommand_mobile)"
					@blur="changeField('recommand_mobile',orgUserBasicInfo.recommand_mobile)">
				</el-input>
			  </el-form-item>
			</el-col>
			<el-col :span="8">
			<el-form-item label="手机号码2" prop="recommand_mobile1">
				<el-input
					v-model="orgUserBasicInfo.recommand_mobile1"
					@blur="changeField('recommand_mobile1',orgUserBasicInfo.recommand_mobile1)">
				</el-input>
			  </el-form-item>
			</el-col>
			<el-col :span="8">
			<el-form-item label="固定号码" prop="recommand_phone">
				<el-input
					v-model="orgUserBasicInfo.recommand_phone"
					@blur="changeField('recommand_phone',orgUserBasicInfo.recommand_phone)">
				</el-input>
			  </el-form-item>
			</el-col>
		</el-row>
		<el-row justify="space-around">
			<el-col :span="24">
			  <el-form-item label="推荐人信息" v-if="!recommandInfo.isEmpty">
          <router-link :to="`/foreground/index/userview/${recommandInfo.data.rec_mobile}/${recommandInfo.data.company_id}/${recommandInfo.data.customer_id}`">
            <el-button size="small">点击查看</el-button>
          </router-link>
			  </el-form-item>
			</el-col>
		</el-row>
			<el-button v-if="status==2"
			type="primary"
			class="margin-bottom10"
			@click="save();" :disabled="isInSave">
			保存
		</el-button>
	</el-form>
</template>
<script>

import cityList from '../common/cityList.vue'
import tagAdder from '../common/tagAdder.vue'
import ElRow from "element-ui/packages/row/src/row";
import ElCol from "element-ui/packages/col/src/col";

export default {
	props: [
		'status',
		'customer_id',
		'company_id',
		'mobile',
		"changeFieldLen1",
		'baseData'
	],
	data: function () {
	  let mobile=(rule,value,callback)=>{
	      if (value==""){
	        return;
        }
        if( !/^1[34578]\d{9}$/.test(value)){
          return callback(new Error('手机号码格式不正确'));
        }
    };
	  let age=(rule,value, callback)=>{
	    if (!/^[1-9][0-9]$|^[1-9]$/.test(value)&&value!=''){
	      callback(new Error("年龄范围为1-99"))
      }
    }
    let phone=(rule,value, callback)=>{
	    console.log('sss')
	    if (!/^0\d{2,3}-?\d{7,8}$/.test(value)&&value!=''){
        callback(new Error("电话号码格式不对"))
      }
    }
		return {
	    isInSave:false,
			city_data: {
				city_code: '',
				single: 'single'
			},
			orgUserBasicInfo: {
				city_name: ''
			},
			userbasicRule:[
				{
					name: "age",
					rule: /^[1-9][0-9]$|^[1-9]$/,
					desc: "年龄范围为1-99"
				},
				{
					name: "mobile",
					rule: /^1[345678]\d{9}$/,
					desc: "手机码号1格式不正确"
				},
				{
					name: "mobile1",
					rule: /^1[345678]\d{9}$/,
					desc: "手机码号2格式不正确"
				},
				{
					name: "recommand_mobile",
					rule: /^1[345678]\d{9}$/,
					desc: "推荐人手机码号1格式不正确"
				},
				{
					name: "recommand_mobile1",
					rule: /^1[345678]\d{9}$/,
					desc: "推荐人手机码号2格式不正确"
				},
        {
          name: "recommand_phone",
          rule: /^0\d{2,3}-?\d{7,8}$/,
          desc: "推荐人固定电话号码格式不正确"
        },
        {
          name:'phone',
          rule:/^0\d{2,3}-?\d{7,8}$/,
          desc:'固定电话号码不对'
        }
			],
			reqRule:[
				{
					name: "prev_year_budget",
					rule: /^[1-9]([0-9]{0,3}|[0-0]{4})$/,
					desc: "会议总预算必须为正整数且不能大于10000万"
				},
				{
					name: "current_year_num",
					rule:  /^[0-9]+$/,
					desc: "会议总次数必须为正整数"
				},
				{
					name: "hxe_percentage",
					rule: /^\d{0,2}$/,
					desc: "通过小二预定占比必须为小于100的正整数"
				},
			],
			sex_arr: [
				{
					id: 1,
					name: "男"
				},
				{
					id: 2,
					name: "女"
				}
			],
			specialty_arr: [
				{
					id: 1,
					name: "专业"
				},
				{
					id: 2,
					name: "非专业"
				}
			],
			show_being_lived_city: false,
			showProfessionUserDef: false,
      showJobUserDef:false,
			tagData: {
				'profession': {
					tag: 'profession',
					tagName: '自定义职业标签'
				},
        'job':{
				  tag:'job',
          tagName:'自定义职位标签'
        }
			},
			mobileExp : /^1[34578]\d{9}$/,
			//以下是提交的数据
			userbasicinfo: {
			},
      recommandInfo:{
			  //是否存在
			  isEmpty:true,
        data:{}
      },
      baseInfoRules:{
			  //elementUI自带验证
        mobile: [ { required: true, message: '请输入手机号', trigger: 'blur' },{
          validator:mobile,
          trigger: 'blur'
        }],
        mobile1: [{
          validator:mobile,
          trigger: 'blur'
        }],
        recommand_mobile: [{
          validator:mobile,
          trigger: 'blur'
        }],
        recommand_mobile1: [{
          validator:mobile,
          trigger: 'blur'
        }],
        recommand_phone:[
          {
            validator:phone,
            trigger: 'blur'
          }
        ],
        age:[{
          validator:age,
          trigger:'blur'
        }],
        phone:[{
          validator:phone,
          trigger:'blur'
        }]
      }
		}
	},
	mounted(){
		let self = this;
		self.getOrgUserBasicInfo();
//		注册验证事件
    self.$root.Bus.$off('baseFormValidate')
    self.$root.Bus.$on('baseFormValidate',self.baseFormValidate)
	},
	watch: {
		'$route' (to, from) {
			let self = this;
			self.resetData();
			self.getOrgUserBasicInfo();
		 }
	},
	methods: {
		resetData() {
			this.show_being_lived_city = false;
			this.showProfessionUserDef = false;
			this.city_data =  {
				city_code: '',
				single: 'single'
			};
			this.orgUserBasicInfo =  {
				city_name: ''
			};
			this.orgUserBasicInfo =  {
				city_name: ''
			};
			this.userbasicinfo = {}
			this.recommandInfo = {
			  //是否存在
			  isEmpty:true,
        data:{}
      }
		},
		getRecommandInfo(mobile){
			let self = this;
      self.recommandInfo.isEmpty=true
      self.userbasicinfo.recommand_id='0'
      self.userbasicinfo.recommand_company_id=''
			if (self.mobileExp.test(mobile)) {
		        self.$ajax.post(self.$api.getModuleHost('CRM', "customer/checkUser"),
              {
                mobile: mobile
              }).then(data=>{
                if(data.code ==1000){
                  if (data.customer_id==0){
                    return
                  }else {
                    self.recommandInfo.isEmpty=false
                    self.recommandInfo.data=data.data
                    self.userbasicinfo.recommand_id=data.customer_id
                    self.userbasicinfo.recommand_company_id=data.company_id
                  }
                }
              })
              .catch(error =>{
                console.log(error)
              })
		    }
		},
		showAddArea(tag){
			let self = this;
			self['show' + tag + 'UserDef'] = !self['show' + tag + 'UserDef'];
		},
		setUserDefined(opt){
			let self = this;
			console.log(opt)
			self.$emit('getUserDefined',opt);
		},
		setCity(opt){
			let self = this;
			self.orgUserBasicInfo.city_name = opt.city_name;
			self.show_being_lived_city = false;
			self.changeField('city',opt.city_code);
		},
		hideCityList(){
			let self = this;
			self.show_being_lived_city = false;
		},
		changeField(feildName,feildVal){
			let self = this;
			if(self.status == 1){
				self.$emit('getUserBasicInfo',arguments);
			} else {
				self.userbasicinfo[feildName] = feildVal;
			}
		},
		getOrgUserBasicInfo(){
			let self = this;
			self.$ajax.get(
				self.$api.getModuleHost('CRM', "customer/userBasicInfo"),
					{
						company_id: self.company_id,
						customer_id: self.customer_id,
						mobile: self.mobile
					}
				)
			.then(function (data) {
				if (data.code == 1000) {
					let storageId = 'draft_' + self.mobile + self.company_id // 缓存id
					if (localStorage.getItem(storageId)) {
						let localData = JSON.parse(localStorage.getItem(storageId))
						self.$emit('getUserBasicInfoAll',localData['userbasicinfo']);
						self.orgUserBasicInfo = localData['userbasicinfo'];
					} else {
						self.$emit('getUserBasicInfoAll',data.data);
						self.orgUserBasicInfo = data.data;
//						排除异常的电话号码
						if (self.orgUserBasicInfo.recommand_mobile==0||self.orgUserBasicInfo.recommand_mobile=="0"){
              self.orgUserBasicInfo.recommand_mobile=""
            }
					}
					self.userbasicinfo = data.data;
          self.getRecommandInfo(self.orgUserBasicInfo.recommand_mobile)
          self.$refs['orgUserBasicInfo'].resetFields()
				}
			}).catch(function (err) {
				console.error(err);
			});
		},
		showBeingLivedCity(){
			let self = this;
			self.show_being_lived_city = true;
		},
		save(){
			let self = this;
			if (self.changeFieldLen1 == 0) {
				self.$emit('getBool');
				return;
			}
			var validateMsg = self.validateForm()
			if (validateMsg !=""){
				self.$message({
          message: validateMsg,
          type: 'warning',
					duration: 1000
        });
				return;
			}
			if (self.isInSave)
			  return
      self.isInSave=true;
			self.$ajax.post(
				self.$api.getModuleHost('CRM', "customer/saveCustomer"),
				{
					customer_id: self.customer_id,
					company_id: self.company_id,
					userbasicinfo: JSON.stringify(self.userbasicinfo)
				}
			)
			.then(function (data) {
				if (data.code == 1000) {
					self.$emit('getBool');
				} else {
					alert(data.msg);
				}
        self.isInSave=false;
			}).catch(function (err) {
				console.error(err);
        self.isInSave=false;
			});
		},
		validateForm() {
			let ruleDesc = "";
			let self = this;
			for (let k in self.userbasicRule){ // 验证基础数据中的格式
				let ruleName = self.userbasicRule[k]['name']

				if(self.userbasicinfo[ruleName]){
					let rule = self.userbasicRule[k]['rule'];

					if (!rule.test(self.userbasicinfo[ruleName])){
						ruleDesc = self.userbasicRule[k]['desc'];
						return ruleDesc;
					}
				}
			}
			return ruleDesc;
		},
    baseFormValidate(){
		  this.$refs['orgUserBasicInfo'].validate()
    }
	},
	components: {
    ElCol,
    ElRow,
    cityList,
		tagAdder
	}
}
</script>
<style lang="less">
	.margin-top8{
		margin-top: 8px;
	}
	.el-radio-group .sex-radio.el-radio{
		width: 50px;
	}
	.specialty-radio.el-radio{
		width: 58px;
	}
	.city-live{
		cursor: pointer;
	}
  .text-link{
    text-decoration:underline;
    color:blue;
  }
	.red-text{
		color: red;
	}
</style>



